import React, {useCallback, useContext, useEffect, useState} from 'react';
import {Typography} from '@mui/material/';
import Wallet from 'components/uikit/widgets/Menu/icons/Wallet'
import Methods from 'components/Methods'
import {useGetCollateralManageState, useUpdataFBTC20ApproveStatus} from "state/hooks";
import {useWeb3React} from "web3-react-core";
import usePanel from "hooks/usePanel";
import tokens, { poolData } from "config/constants/tokens";
import {CollateralProps, kovanAddresses} from "config/constants/types";
import {useAddGuarantee, useGetTokenBalance, useTransfer} from "hooks/useManager";
import { useApprove, useCheckApprove } from "hooks/useApprove";
import useToast from "hooks/useToast";
import contracts from "config/constants/contracts";
import {
  Wrapper,
  Title,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Approve,
  DataWrap,
  IconWrap,
  TipWrap,
  // eslint-disable-next-line import/named
  icon, FlexWrap, AlertButton
} from "components/util";
import { PositionContext } from "contexts/PositionContext";
import {BigNumber} from "@ethersproject/bignumber";
import {useAppSelector} from "../../../state";
import {ManagerContext} from "../../../contexts/ManagerContext";
import {formatWBTC} from "../../../utils/formatTheValue";

/* export function FromWalletAddToGuarantee() {

  return ()
} */



export default function CustomizedAccordions({managerInfo}) {
  // @ts-ignore
  const {managerData} = useContext(ManagerContext)
  // @ts-ignore
  const { positionInfo, walletTokenBalance, avatarAssetList, position, refresh, refreshStatus } = useContext(PositionContext)
  const {expanded, handleChange} = usePanel();
  const {toastSuccess, toastError, toastInfo} = useToast()
  const [options,] = useState(poolData)
  const [m1LfVal,setM1LfVal] = useState<number>();
  const [m2LfVal,setM2LfVal] = useState<number>();
  const [guaranteeBalance,setGuaranteeBalance] = useState()
  const {positionManagerFBTC20} = contracts;
  const {active, chainId, account} = useWeb3React();
  const currentManagerAddress = useAppSelector(state=> state.collateralManage.currentManagerAddress)
  // const {getTokenBalance} = useGetTokenBalance();
  const {handleApprove} = useApprove(account, tokens.WBTC.address[chainId])
  const FBTC20ApproveStatus = useGetCollateralManageState('FBTC20ApproveStatus')
  const {onFBTC20ApproveStatus} = useUpdataFBTC20ApproveStatus()
  const {addGuarantee} = useAddGuarantee(positionManagerFBTC20.abi, positionManagerFBTC20.address[chainId])
  const {onCheckApprove} = useCheckApprove();
  const transfer = useTransfer();
  const onInput = (e)=>{
    setM1LfVal(e.target.value);
  }
  const onMax = ()=>{
    // @ts-ignore
    setM1LfVal(walletTokenBalance.get('WBTC') / BigNumber.from(10).pow(8))
  }
  const onApprove = useCallback(async ()=>{
    // if (FBTC20ApproveStatus) return;
    if (active && chainId && managerData) {
      try {
        const info = await handleApprove(managerData.address[chainId]);
        if (info.hash) {
          const result = await info.wait();
          if (result) {
            onFBTC20ApproveStatus({status: true})
            toastSuccess('Approve Success')
          }
        }
      } catch ({message}) {
        toastError('Failed',`${message}`)
      }

    }
  }, [active, chainId, managerData, handleApprove, onFBTC20ApproveStatus, toastSuccess, toastError])

  const onSubmit = useCallback(async ()=>{
    if (active && position) {
      try {
        // @ts-ignore
        console.log('.....', position, String(BigNumber.from(10).pow(tokens.WBTC.decimals).toString() * m1LfVal))
        // @ts-ignore
        const txInfo = await addGuarantee(position,String(BigNumber.from(10).pow(tokens.WBTC.decimals).toString() * m1LfVal));
        console.log("txInfo>>>", txInfo)
        if (txInfo) {
          toastInfo('Submitted ', (
            <>
              <p style={{marginBottom: 24}}>Waiting for confirmation</p>
              <FlexWrap style={{flexDirection: 'row-reverse'}}>
                <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txInfo.hash}`)}}>View on Explorer</AlertButton>
              </FlexWrap>
            </>
          ))
          const result = await txInfo.wait();
          console.log('result>>>>', result)
          if (result) {
            toastSuccess('Submitted', (
              <>
                <p style={{marginBottom: 24}}>{managerInfo.avatar} collateral account was opened.
                  You can add collateral first and then go to mint the token.</p>
                <FlexWrap style={{flexDirection: 'row-reverse'}}>
                  <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/address/${managerInfo.avatar}`)}}>View on Explorer</AlertButton>
                </FlexWrap>
              </>
            ))
            refresh(state=>!state)
          }
        }
      } catch ({message}) {
        toastError('Failed', message)
      }
    }
  }, [active,refresh, addGuarantee, m1LfVal, managerInfo, toastInfo, toastSuccess, toastError, position])

  //
  const onTransfer = useCallback(async ()=>{
    if (active && account && position) {
     // @ts-ignore
      const tx = await transfer(account, tokens.WBTC.address[chainId], position, String(BigNumber.from(10).pow(tokens.WBTC.decimals).toString() * m2LfVal));
      console.log("tx>>>", tx)
      if (tx) {
        const transferInfo = await tx.wait();
        console.log(transferInfo)
        if (transferInfo) {
          refresh(state=> !state)
        }
      }
    }
  }, [active, account, position, transfer, chainId, m2LfVal, refresh])


  /**
   *  checkApprove
   *
   * */
  useEffect(()=>{
    (async ()=>{
      if (active && account) {
        const txInfo = await onCheckApprove( account, tokens.WBTC.address[chainId], currentManagerAddress);
        console.log('txInfo>>>>', txInfo)
        if (txInfo && txInfo._hex > 0) {
          onFBTC20ApproveStatus({status: true})
        }else{
          onFBTC20ApproveStatus({status: false})
        }
      }
    })();
  }, [account, active, chainId, onCheckApprove, onFBTC20ApproveStatus, currentManagerAddress])

  /**
   * getWalletTokenBalance
   * */
 /* useEffect(()=>{
    if (active && !walletWbtcBalance && account) {
      getTokenBalance(account).then(res=>{
        if (res) {
          // @ts-ignore
          setWalletBtcBalance(res.WBTC)
        }
      })
    }
  }, [active,account, getTokenBalance, walletWbtcBalance, toastInfo])
*/

  useEffect(()=>{
    if (positionInfo) {
      // @ts-ignore
      setGuaranteeBalance(formatWBTC(positionInfo[5]))
    }
  }, [positionInfo, refreshStatus])



  return (
    <Wrapper>
      <Title>Add collateral before mint</Title>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography>Method 1. Add asset from my wallet to guarantee pool</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Methods
            key={positionInfo || -212}
            SelectIcon={Wallet} options={options}
            rightBalance={guaranteeBalance || 0} rightOptions={options}
            leftBalance={walletTokenBalance?walletTokenBalance.get('WBTC'): 0}
            rightSelectedValue={42} rightSymbol="WBTC" selectValue={42} selectedBalance={100} selectedSymbol="WBTC"
            RightIcon={icon(IconWrap, 'G')}
            leftInputValue={m1LfVal}
            rightInputValue={m1LfVal}
            selectInput={onInput}
            onMax={onMax}
          />
          <DataWrap>
            <div className="ratio">
              <p>Collateral Ratio</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
            <div className="days">
              <p>Guarantee Days</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
          </DataWrap>
          <Approve style={{boxShadow: 'none'}} onClick={FBTC20ApproveStatus?onSubmit:onApprove}>{FBTC20ApproveStatus?'Submit':'Approve'}</Approve>
          {/* <Approve style={{boxShadow: 'none'}} onClick={onSubmit}>Submit</Approve> */}
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography>Method 2. Add asset from my wallet to Avatar</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Methods
            SelectIcon={Wallet} options={options} rightBalance={avatarAssetList ? avatarAssetList.get('WBTC') : 0} rightOptions={options}  leftBalance={walletTokenBalance?walletTokenBalance.get('WBTC'): 0}
            rightSelectedValue={42} rightSymbol="WBTC" selectValue={42} selectedBalance={100} selectedSymbol="WBTC"
            leftInputValue={m2LfVal}
            rightInputValue={m2LfVal}
            selectInput={({target})=>{
              // @ts-ignore
              setM2LfVal(target.value);
            }}
            RightIcon={icon(IconWrap, 'A')}/>
          <DataWrap>
            <div className="ratio">
              <p>Guarantee Ratio</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
            <div className="days">
              <p>Guarantee Ratio</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
          </DataWrap>
          <TipWrap>
            <IconWrap style={{background: "#FF7D00", color: '#fff'}}>!</IconWrap>
            <p>Add asset to avatar won’t effect the guarantee days, so you might need to add the specific asset in
              method 1 first.</p>
          </TipWrap>
          <Approve style={{boxShadow: 'none'}} onClick={FBTC20ApproveStatus?onTransfer:onApprove}>{FBTC20ApproveStatus ?'Submit':'Approve'}</Approve>

        </AccordionDetails>
      </Accordion>
    </Wrapper>
  );
}
