import React, {useContext} from 'react'
import Switch from 'components/Switch'
import { styled } from '@mui/material/styles'
import Card from 'components/Card'
import { CopyIcon, ArrowLine } from 'components/uikit'
import { List, ListItem, Divider } from '@mui/material'
import {ManagerContext} from "contexts/ManagerContext";
import {PositionContext} from "contexts/PositionContext";
import tokens from "config/constants/tokens";
import formatTheValue, { formatWBTC } from 'utils/formatTheValue'
import CopyToClipboard from "components/uikit/widgets/WalletModal/CopyToClipboard";

const TitleContent = styled('h3')`
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
  margin-bottom: 20px;
`
const LeftWrap = styled(Card)`
  background: rgba(255, 255, 255, 0.4);
  margin-bottom: 20px;
`

const AssetListWrap = styled('div')`
  .total {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 14px;
    span {
      color: #000000;
    }
  }
  .guarantee,
  .avatar,
  .ticker {
    display: flex;
    margin-bottom: 16px;
    .icon {
      width: 20px;
      border-radius: 50%;
      display: block;
      margin-right: 8px;
      background-color: #ccc;
    }
    div {
      line-height: 20px;
      width: calc(100% - 20px - 8px);
      display: flex;
      justify-content: space-between;
      span:last-of-type {
        font-size: 12px;
        color: rgba(0, 0, 0, 0.5);
      }
    }
  }
  .avatar .avatar_info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 120px;
    svg {
      cursor: pointer;
    }
  }
  .ticker div span {
      font-size: 14px !important;
      line-height: 20px;
      color: #000000 !important;
  }
  .token_list {
    background: rgba(0, 0, 0, 0.04);
    border-radius: 8px;
    .MuiListItem-root {
      display: flex;
      .currency {
        width: 100%;
        margin-left: 12px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 12px;
        color: rgba(0, 0, 0, 0.5);
      }
    }
  }
  .daily {
    margin-top: 20px;
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #000000;
  }
  .daily_name {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #000000;
  }
  .more {
    margin-top: 20px;
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
    color: #2aa1ff;
  }
`

export default function MyCollateral() {
  // @ts-ignore
  const {position, totalCollateralValue, positionValuesInfo, avatarAssetList} = useContext(PositionContext)


  const assetList = ()=>{
    if (!avatarAssetList) return [];
    const temp = [];
    // eslint-disable-next-line no-restricted-syntax
    for (const [token, balance] of avatarAssetList) {
      temp.push(
        <ListItem key={token}>
          <span style={{ width: 20, height: 20, borderRadius: '50%', background: '#ddd' }} />
          <div className="currency">
            <span className="symbol">{token}</span>
            <span className="balance">{balance}</span>
          </div>
        </ListItem>
      )
    }
    return  temp;
  }


  return (
    <>
      <LeftWrap>
            <TitleContent>My Collateral</TitleContent>
            <AssetListWrap>
              <div className="total">
                <span>Total Collateral Value</span>
                <span key={totalCollateralValue}>≈ {totalCollateralValue? formatWBTC(totalCollateralValue) : 0} WBTC</span>
              </div>
              <div className="guarantee">
                <img src="" className="icon" alt="" />
                <div>
                  <span>Guarantee</span>
                  <span key={positionValuesInfo}>≈ {positionValuesInfo? formatWBTC(positionValuesInfo.get('interestGuaranteed')) : 0} WBTC</span>
                </div>
              </div>
              <div className="avatar">
                <img src="" className="icon" alt="" />
                <div>
                  <span>Avatar</span>
                  {/* @ts-ignore */}
                  <CopyToClipboard  className="avatar_info" toCopy={position}>
                    <span key={position}>{position? `${position.slice(0,3)}..${position.slice(5,8)}`: ''}</span>
                  </CopyToClipboard>
                  {/* <span>

                    <CopyIcon />
                  </span> */}
                </div>
              </div>
              <List className="token_list" key={avatarAssetList}>
                {assetList()}

                <ListItem>
                  <Divider style={{ width: '100%' }} />
                </ListItem>
                <ListItem>
                  <span style={{ width: 20, height: 20, borderRadius: '50%', background: '#ddd' }} />
                  <div className="currency">
                    <span className="symbol">Hide Small Balances</span>
                    <Switch />
                  </div>
                </ListItem>
              </List>
              <h3 className="daily">Daily Obligation Reference</h3>
              <div className="daily_name">
                <span>0.00000580 WBTC</span> —&gt; <span> 0.00001580 WBTC</span>
              </div>
              <div className="more">
                <span>Add More Collateral</span>
                <ArrowLine />
              </div>
            </AssetListWrap>
          </LeftWrap>
          <LeftWrap>
            <TitleContent>My Outstanding Debt.</TitleContent>
            <AssetListWrap>
              <div className="ticker">
                <img src="" className="icon" alt="" />
                <div>
                  <span>F(BTC, 20)</span>
                  <span>100.00 —&gt; 120.00</span>
                </div>
              </div>
            </AssetListWrap>
          </LeftWrap>
    </>
  )
}
