import {createReducer} from "@reduxjs/toolkit";
// eslint-disable-next-line import/named
import { HoldingsProps } from "config/constants/types";
import {updataHoldingsWallet} from "./action";

export interface HoldingsState {
  holdingsWallet: HoldingsProps[]
  holdingsAvatar: HoldingsProps[]
}



const initialState: HoldingsState = {
  holdingsWallet: [
    {
      ticker: 'F(BTC,20)',
      markPrice: 20,
      markSymbol: 'WBTC',
      holding: 200,
      claimableRewards: 0.0002
    }
  ],
  holdingsAvatar: [],
}

export default createReducer(initialState, (builder)=>
  builder.addCase(updataHoldingsWallet, (state, {payload})=>{
    state.holdingsWallet = payload.list
  })
)
