import React, { useLayoutEffect } from 'react'
import { styled } from '@mui/material/styles'
import { Grid, Input, FormControlLabel, Checkbox, CheckboxProps } from '@mui/material'
import Card from 'components/Card'
import { useWeb3React } from 'web3-react-core'
import { Button } from 'components/uikit/components/Button'

const Wrapper = styled('div')`
  padding-right: 44px;
`

const Title = styled('h2')`
  font-weight: bold;
  font-size: 32px;
  line-height: 47px;
  color: #000000;
`
const SubTitle = styled('p')`
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: rgba(0, 0, 0, 0.5);
  margin-bottom: 20px;
`

const CardWrap = styled(Card)`
  h3 {
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    color: #000000;
    margin-bottom: 16px;
  }
  .MuiInputBase-root {
    margin-bottom: 30px;
    width: 100%;
    background: rgba(255, 255, 255, 0.4);
    font-weight: 600;
    border: 1px solid rgba(207, 216, 226, 0.5);
    box-sizing: border-box;
    border-radius: 11px;
    padding: 12px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.5);

    &::before {
      content: '';
      border: 0;
    }
    input {
      padding: 0;
      border: none;
    }
    
  }
  .addToWallet {
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #2aa1ff;
    margin-bottom: 30px;
    .btn {
      cursor: pointer;
      padding: 10px 15px;
    }
  }

  .link {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.02em;
      color: #2aa1ff;
      text-align: center;
      cursor: pointer;
    }
`

const ButtonWrap = styled(Button)`
  display: block;
  margin: 43px auto 20px;
  padding: 13px 53px;
  box-shadow: none;
  background: linear-gradient(90deg, #61c2f7 0%, #7ae9f0 100%);
  border-radius: 20px;
`

const BpIcon = styled('span')(({ theme }) => ({
  width: 16,
  height: 16,
  borderRadius: '50%',
  boxShadow:
    theme.palette.mode === 'dark'
      ? '0 0 0 1px rgb(16 22 26 / 40%)'
      : 'inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)',
  backgroundColor: theme.palette.mode === 'dark' ? '#394b59' : '#f5f8fa',
  backgroundImage:
    theme.palette.mode === 'dark'
      ? 'linear-gradient(180deg,hsla(0,0%,100%,.05),hsla(0,0%,100%,0))'
      : 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
  '.Mui-focusVisible &': {
    outline: '2px auto rgba(19,124,189,.6)',
    outlineOffset: 2,
  },
  'input:hover ~ &': {
    backgroundColor: theme.palette.mode === 'dark' ? '#30404d' : '#ebf1f5',
  },
  'input:disabled ~ &': {
    boxShadow: 'none',
    background: theme.palette.mode === 'dark' ? 'rgba(57,75,89,.5)' : 'rgba(206,217,224,.5)',
  },
}))

const BpCheckedIcon = styled(BpIcon)({
  backgroundColor: '#137cbd',
  backgroundImage: 'linear-gradient(270deg, #7AE9F0 0%, #61C3F7 100%)',
  boxShadow: 'none',
  '&:before': {
    display: 'block',
    width: 16,
    height: 16,
    backgroundImage:
      "url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath" +
      " fill-rule='evenodd' clip-rule='evenodd' d='M12 5c-.28 0-.53.11-.71.29L7 9.59l-2.29-2.3a1.003 " +
      "1.003 0 00-1.42 1.42l3 3c.18.18.43.29.71.29s.53-.11.71-.29l5-5A1.003 1.003 0 0012 5z' fill='%23fff'/%3E%3C/svg%3E\")",
    content: '""',
  },
  'input:hover ~ &': {
    backgroundColor: '#106ba3',
  },
})

const TokenWrap = styled('div')`
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(207, 216, 226, 0.5);
  box-sizing: border-box;
  padding: 15px;
  border-radius: 11px;
  .MuiFormControlLabel-root {
    width: 50%;
    border-right: 1px solid rgba(207, 216, 226, 0.5);
  }
  div.MuiInput-root {
    border: none;
    padding: 0;
    background-color: transparent;
    width: 50%;
    margin: 0;
  }
`

// Inspired by blueprintjs
function BpCheckbox(props: CheckboxProps) {
  return (
    <Checkbox
      sx={{
        '&:hover': { bgcolor: 'transparent' },
      }}
      disableRipple
      color="default"
      checkedIcon={<BpCheckedIcon />}
      icon={<BpIcon />}
      inputProps={{ 'aria-label': 'Checkbox demo' }}
      {...props}
    />
  )
}

export default function Faucet() {
  const { account } = useWeb3React()

  useLayoutEffect(() => {
    if (window) {
      setTimeout(() => {
        // @ts-ignore
        window.grecaptcha.enterprise.render('html_element', {
          sitekey: '6LfltOQeAAAAAIP6szEO258U7sOBWRhAkYaUCiGE',
          callback(res) {
            console.log(res)
          },
        })
      }, 2000)
    }
  })

  return (
    <Wrapper>
      <Grid container>
        <Grid item xs={8}>
          <Title>Faucet</Title>
          <SubTitle>You can claim the test tokens from the faucet once every 24 hours.</SubTitle>

          {/*   <TipWrap style={{ background: 'rgba(255, 255, 255, 0.6)', display: 'flex', alignItems: 'center' }}>
            <IconWrap style={{ background: '#FF7D00', color: '#fff' }}>!</IconWrap>
            <p>
              Add asset to avatar won’t effect the guarantee days, so you might need to add the specific asset in method
              1 first.
            </p>
          </TipWrap>
 */}
          <CardWrap>
            <h3>Wallet address</h3>
            <Input value={account} disabled type="text" />
            <h3>Request tokens</h3>
            <Grid container spacing={4}>
              <Grid item xs={6}>
                <TokenWrap>
                  <FormControlLabel control={<BpCheckbox defaultChecked />} label="test WBTC" labelPlacement="end" />
                  <Input type="number" disabled value="15.00" dir="rtl" />
                </TokenWrap>
              </Grid>
              <Grid item xs={6}>
                <TokenWrap>
                  <FormControlLabel control={<BpCheckbox defaultChecked />} label="test USDT" labelPlacement="end" />
                  <Input type="number" disabled value="800" dir="rtl" />
                </TokenWrap>
              </Grid>
            </Grid>
            <Grid container spacing={4} className="addToWallet">
              <Grid item xs={6}>
                <div className="btn">add to Metamask</div>
              </Grid>
              <Grid item xs={6}>
                <div className="btn">add to Metamask</div>
              </Grid>
            </Grid>
            <div id="html_element" />
            <ButtonWrap disabled>Send Request</ButtonWrap>
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <p className="link">Request Ethereum Kovan testnet ETH from Chainlink faucet</p>
          </CardWrap>
        </Grid>
      </Grid>
    </Wrapper>
  )
}
