import React, {useCallback, useContext, useEffect, useState} from 'react';
import {Grid, ButtonBase, Input, TextField} from '@mui/material'
import NumberFormat from 'react-number-format'
import {ManagerContext} from "contexts/ManagerContext";
import {PositionContext} from "contexts/PositionContext";
import {styled} from "@mui/material/styles";
import Card from 'components/Card'
import Select from 'components/Select'
import {kovanAddresses, tokensSymbol} from "config/constants/types";
import tokens from "config/constants/tokens";
import {Button} from 'components/uikit/components/Button'
import {usePositionFBTC20Mint} from "hooks/useManager";
import {useWeb3React} from "web3-react-core";
import {BigNumber} from "@ethersproject/bignumber";
import ListStyleOne from './icon/ListStyleOne'
import ListStyleTwo from './icon/ListStyleTwo'
import Range from './Range'
import useToast from "../../../hooks/useToast";
import toast from "../../../components/uikit/widgets/Toast/Toast";
import {AlertButton, FlexWrap} from "../../../components/util";

const Wrapper = styled('div')`

`

const CardWrap = styled(Card)`
  padding: 33px 33px 30px 65px;
  box-shadow: 0 4px 27px rgba(0, 0, 0, 0.03);
  backdrop-filter: unset;
`
const Title = styled('h2')`
  display: flex;
  align-content: space-between;
  margin-bottom: 20px;

  span:first-of-type {
    font-weight: 600;
    font-size: 22px;
    line-height: 31px;
    color: #000000;
  }
`
const SelectGroup = styled('div')`
  button.MuiSelectUnstyled-root {
    width: 200px;
  }

  div.MuiInput-root {
    width: calc(100% - 200px) !important;
  }
`

const Func = styled('div')`
  padding: 10px 20px;

  button {
    color: #2AA1FF;
  }
`

const SubTitle = styled('span')`
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.5);
`
const GridItem = styled(Grid)`
  position: relative;

  .listStyle {
    position: absolute;
    top: 0;
    width: 30px;
    transform: translateX(calc(-100% - 10px));
  }
`
const MintInfoWrap = styled(Grid)`
  background: rgba(0, 0, 0, 0.04);
  border-radius: 8px;
  width: 100%;
  padding: 24px;
  margin-bottom: 10px;

  h4 {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.5);

  }

  p:first-of-type {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #000000;
  }

  p + p {
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.5);
  }

  .MuiGrid-root {
    position: relative;
  }

  .MuiGrid-root:first-of-type {
    &:after {
      content: '';
      position: absolute;
      right: 24px;
      top: 0;
      display: block;
      height: 100%;
      width: 1px;
      background-color: rgba(0, 0, 0, 0.08);
    }
  }
`
const Tip = styled('p')`
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  color: #000000;
`
const MintButton = styled(Button)`
  background: linear-gradient(90deg, #61C2F7 0%, #7AE9F0 100%);
  border-radius: 20px;
  box-shadow: none;
  line-height: 46px;
  height: 46px;
  display: block;
  margin: 30px auto 0;
  width: 30%;
  color: #000;
`
const SetMintWrap = styled('div')`
  position: relative;
  width: 100%;
  margin-bottom: 12px;
  padding-left: 20px;
  display: flex;
  font-weight: 600;
  font-size: 14px;
  color: #000;
  align-items: center;

  .range_wrap {
    width: calc(100% - 80px - 20px);
    margin-right: 30px;

    .MuiSlider-root {
      width: 100%;
    }
  }

  .MuiInput-root {
    width: 80px;
    height: 30px;
    padding: 5px 20px;
    background: rgba(255, 255, 255, 0.4);
    border: 1px solid rgba(207, 216, 226, 0.5);
    box-sizing: border-box;
    border-radius: 11px;

    &:after, &:before {
      display: none;
    }

    input {
      font-weight: 600;
      font-size: 14px;
      color: #000;
      padding-right: 30px;
      padding-right: 2px;
      text-align: center;
    }

    input::-webkit-inner-spin-button {
      display: none;
    }
  }

  .input {
    padding-top: 10px;
    position: relative;
  }

  .unit {
    position: absolute;
    top: calc(50% + 5px);
    right: 5px;
    transform: translateY(-50%);
  }
`


const MintData = [
  {
    token: tokensSymbol.FBTC20,
    symbol: tokensSymbol.FBTC20,
    address: {
      42: kovanAddresses.FBTC20
    },
    balance: 0,
    chainId: 42
  }
]

interface CustomProps {
  onChange: (event: { target: { name: string; value: string } }) => void;
  name: string;
}

// @ts-ignore
const NumberFormatCustom = React.forwardRef<NumberFormat, CustomProps>(
  function NumberFormatCustom(props, ref) {
    const {onChange, ...other} = props;

    return (
      <NumberFormat
        {...other}
        getInputRef={ref}
        onValueChange={(values) => {
          onChange({
            target: {
              name: props.name,
              value: values.value,
            },
          });
        }}
        thousandSeparator
        isNumericString
        prefix="$"
      />
    );
  },
);

function Mint(props) {
  // @ts-ignore
  const {managerData, tokenPriceInfo, collateralParams} = useContext(ManagerContext)
  const {hidden} = props;
  const {library, active, account} = useWeb3React()
  // @ts-ignore
  const {estimatedDailySingleDebit, onComputedCollateralRatio, guaranteeDays, positionInfo } = useContext(PositionContext)
  const {toastError, toastWarning, toastSuccess, toastInfo} = useToast()
  const [rangeVal, setRangeVal] = useState(0);
  const [val, setVal] = useState(100);
  const [mintAmount,setMintAmount] = useState(0)
  const [assetVal,setAssetVal] = useState<number>()
  const [forecastGuaranteeDays,setForecastGuaranteeDays] = useState()
  const [mintStatus,setMintStatus] = useState(false)

  const onMax = useCallback(() => {
    console.log('to Max')
  }, [])
  const onRange = useCallback((event, v) => {
    setRangeVal(v)
    setVal(v * 5 + 100)
  }, [])
  const onInput = useCallback(({target}) => {
    const {value} = target;
    setRangeVal((value - 100) / 5)
    setVal(value)
  }, [])

  // eslint-disable-next-line consistent-return
  const onMint = useCallback(async ()=> {
    if (managerData) {
      console.log(BigNumber.from(10).pow(tokens[managerData.token].decimals).mul(mintAmount))
      // @ts-ignore
      if (collateralParams && forecastGuaranteeDays< collateralParams.get('minGuaranteeDays').toString()) {
        toastWarning('Mint is not allowed', 'GuaranteeDays less than minimum GuaranteeDays');
        return false;
      }

      const info = await managerData.contract.mint(BigNumber.from(10).pow(tokens[managerData.token].decimals).mul(mintAmount));
      if (info) {
        toastInfo('Submitted ', (
          <>
            <p style={{marginBottom: 24}}>Waiting for confirmation</p>
            <FlexWrap style={{flexDirection: 'row-reverse'}}>
              <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${info.hash}`)}}>View on Explorer</AlertButton>
            </FlexWrap>
          </>
        ))
        setMintStatus(true)
        const result = await info.wait();
        if (result) {
          toastSuccess('Success')
        }
        setMintStatus(false)
      }
    }
  }, [collateralParams, forecastGuaranteeDays, managerData, mintAmount, toastWarning, toastSuccess, toastInfo])

  const onChangeMintAmount = useCallback(({target})=>{
    const {value} = target;
    setMintAmount(value);
    onComputedCollateralRatio(value, (ratio, _allMintAmount)=>{
      if (estimatedDailySingleDebit) {
        // @ts-ignore
        setForecastGuaranteeDays(Math.floor(positionInfo[5] / (_allMintAmount * estimatedDailySingleDebit)))
      }

      // setAllMintAmount(_allMintAmount)
      setRangeVal((ratio * 100 - 100) / 5)
      setVal(ratio * 100)
    });
  }, [estimatedDailySingleDebit, onComputedCollateralRatio, positionInfo])

  return (
    <Wrapper hidden={hidden}>
      <CardWrap>
        <Grid container>
          <GridItem item xs={12}>
            <ListStyleOne className="listStyle"/>
            <Title>
              <span>Choose an Asset to Mint</span></Title>
            <SelectGroup>
              <Select actionValue={mintAmount} selectValue={tokensSymbol.FBTC20} groupStyle={{width: '100%'}} selectInput={onChangeMintAmount} options={MintData} isGroup/>
              <Func dir='rtl'><ButtonBase onClick={onMax}>Max</ButtonBase></Func>
            </SelectGroup>
          </GridItem>
          <GridItem item xs={12}>
            <ListStyleTwo className="listStyle"/>
            <Title>
              <div>
                <span>Set a Mint Collateral Ratio</span>
                <SubTitle>Set a collateral above the Min. ratio and the Min. Guarantee days</SubTitle>
              </div>
            </Title>
            {/* progress start */}
            <SetMintWrap>
              <Range onChange={onRange} value={rangeVal}/>
              <div className='input'>
                <Input type="number" onInput={onInput}
                       value={val}
                />
                <span className='unit'>%</span>
              </div>
            </SetMintWrap>
            {/* progress end */}
            <MintInfoWrap container>
              <Grid item xs={6}>
                <h4>Guarantee days</h4>
                <p>
                  <span>{guaranteeDays? guaranteeDays.toString(): 0}</span>
                  ——&gt;
                  <span>{forecastGuaranteeDays || 0}</span></p>
                <p>Min. Guarantee: {collateralParams ? collateralParams.get('minGuaranteeDays').toString() : 0} days</p>
              </Grid>
              <Grid item xs={6}>
                <h4>Mark Price</h4>
                {/* @ts-ignore */}
                <p>1 F(BTC, 20) ≈ {tokenPriceInfo? tokenPriceInfo.get('markPrice') / BigNumber.from(10).pow(tokens.WBTC.decimals).toNumber() / 10000 : 0} WBTC</p>
              </Grid>
            </MintInfoWrap>
            <Tip>A 1.0% fee of the token value will be levied after the position is neutralized.</Tip>
          </GridItem>
        </Grid>
        <MintButton disabled={mintStatus} onClick={onMint}>Mint</MintButton>
      </CardWrap>
    </Wrapper>
  );
}

export default Mint;
