import React, {useCallback, useEffect, useState} from 'react'
import {useManagerList} from 'hooks/useManager'
import {useAppSelector} from "state";
import {useWeb3React} from "web3-react-core";
import contracts from 'config/constants/contracts'
import tokens from 'config/constants/tokens'
import {BigNumber} from "@ethersproject/bignumber";
import {getContract} from "utils/web3React";




// @ts-ignore
const ManagerContext = React.createContext()

const ManagerContextProvider = ({ children },context) => {
  const [managerData,setManagerData] = useState();
  const managerList = useManagerList();
  const {oracleHub} = contracts;
  const {chainId, account} = useWeb3React()
  const currentManagerAddress = useAppSelector(state=>state.collateralManage.currentManagerAddress)
  const [tokenPriceInfo,setTokenPriceInfo] = useState()
  const [collateralParams,setCollateralParams] = useState()

  const initData = useCallback(() => {
    if (managerList && currentManagerAddress) {
      const _data = managerList.filter(it => {
        return it.address[chainId] === currentManagerAddress
      });
      if (_data.length) {
        setManagerData(_data[0])
      }
    }
  }, [chainId, currentManagerAddress, managerList])
  // updata managerData;
  useEffect(()=>{
    initData()
    return ()=>{
      setManagerData(null)
    }
  }, [initData, account])

  useEffect(()=>{
    if (managerData) {
      // @ts-ignore
      const contract = getContract(managerData.signer, oracleHub.abi, oracleHub.address[chainId]  );
      if (contract) {
        (async ()=>{
          // @ts-ignore
          const info = await contract.getPrice(tokens[managerData.token].oracleHubCallName, BigNumber.from(10000).mul(BigNumber.from(10).pow(tokens[managerData.token].decimals)))
          if (info) {
            // @ts-ignore
            setTokenPriceInfo(new Map(Object.entries(info)))
          }
        })();
      }
    }
    return ()=>{
      setTokenPriceInfo(null)
    }
  }, [chainId, managerData, oracleHub])


  useEffect(()=>{
    if (managerData) {
      // @ts-ignore
      managerData.contract.getCollateralParams().then(res=>{
        if (res) {
          const data = new Map(Object.entries(res));
          // @ts-ignore
          setCollateralParams(data);
        }
      })
    }
    return ()=>{
      setCollateralParams(null)
    }
  }, [managerData])

  return <ManagerContext.Provider value={{managerData,setManagerData, tokenPriceInfo, collateralParams}}>{children}</ManagerContext.Provider>
}

export { ManagerContext, ManagerContextProvider }
