import {useWeb3React} from "web3-react-core";
import {useCallback} from "react";
import {ethers } from "ethers";
import {getContract, getSigner} from 'utils/web3React'
import ERC20 from 'config/abi/erc20.json'

export function useCheckApprove() {
  const { active, library } = useWeb3React()
  // eslint-disable-next-line consistent-return
  const handleApprove = useCallback(async (account,spender, managerAddress)=>{
    if (active) {
      const signer = getSigner(library, account)
      const contract = getContract(signer,ERC20, spender);
      if (contract) {
        return contract.allowance(account,managerAddress)
      }
    }
  },[active, library])

  return {onCheckApprove: handleApprove}
}

export function useApprove(owner,spender){
  const { active, library } = useWeb3React()
  // eslint-disable-next-line consistent-return
  const handleApprove = useCallback(async (target)=>{
      if (active) {
        const signer = getSigner(library, owner)
        const contract = getContract(signer,ERC20,spender);
        if (contract) {
          return contract.approve(target, ethers.constants.MaxUint256)
        }
      }
  },[active, library, owner, spender])

  return {handleApprove}
}
