import React, {useCallback, useContext, useEffect, useState} from 'react'
import {useWeb3React} from "web3-react-core";
import formatTheValue from 'utils/formatTheValue'
import {BigNumber} from "@ethersproject/bignumber";
import {ManagerContext} from "./ManagerContext";
import {useGetTokenBalance, useGetGuaranteesInfo} from "../hooks/useManager";
import tokens from "../config/constants/tokens";
import {getContract} from "../utils/web3React";

// @ts-ignore
const PositionContext = React.createContext()

const PositionContextProvider = ({children, position}) => {
  // @ts-ignore
  const {managerData, tokenPriceInfo} = useContext(ManagerContext);
  const [refresh, setRefresh] = useState(false)
  const {active, library, chainId, account} = useWeb3React()
  const [walletTokenBalance, setWalletTokenBalance] = useState();
  const [avatarAssetList, setAvatarAssetList] = useState();
  const [positionValuesInfo, setPositionValuesInfo] = useState()
  const [totalCollateralValue, setTotalCollateralValue] = useState()
  const [positionInfo, setPositionInfo] = useState()
  const [guaranteeDays, setGuaranteeDays] = useState()
  const [estimatedDailySingleDebit, setEstimatedDailySingleDebit] = useState()
  // const positionInfo = useGetGuaranteesInfo(managerData, position, active);
  const getTokenBalance = useGetTokenBalance(library, active, chainId);

  const onComputedCollateralRatio = useCallback((_mintAmount, callback) => {
    if (totalCollateralValue && tokenPriceInfo && managerData) {
      console.log(managerData)
      const tokenMarkPrice = tokenPriceInfo.get('markPrice').div(10000);
      let mintAllAmount: BigNumber;
      if (_mintAmount > 0) {
        // @ts-ignore
        mintAllAmount = positionInfo[0].div(BigNumber.from(10).pow(tokens[managerData.token].decimals)).add(_mintAmount)
      } else {
        // @ts-ignore
        mintAllAmount = positionInfo[0].div(BigNumber.from(10).pow(tokens[managerData.token].decimals))
      }
      // @ts-ignore
      const ratio = totalCollateralValue / (mintAllAmount.mul(tokenMarkPrice))

      // eslint-disable-next-line no-unused-expressions
      callback && callback(ratio, mintAllAmount);
    }

  }, [totalCollateralValue, tokenPriceInfo, positionInfo, managerData])

  useEffect(() => {
    if (active && managerData && position) {
      (async () => {
        try {
          const info = await managerData.contract.positionInfo(position)
          if (info) {
            setPositionInfo(info);
          }
        } catch (e) {
          //
        }
      })();
    }
    return ()=>{
      setPositionInfo(null)
    }
  }, [active, managerData, position, refresh, account])

  useEffect(() => {
    if (position && account) {
      (async () => {
        const assetList = await getTokenBalance(position)
        const walletAssets = await getTokenBalance(account)
        if (assetList) {
          // @ts-ignore
          setAvatarAssetList(assetList);
        }
        if (walletAssets) {
          // @ts-ignore
          setWalletTokenBalance(walletAssets)
        }
      })();
    }
    return ()=>{
      setWalletTokenBalance(null);
      setAvatarAssetList(null)
    }
  }, [account, getTokenBalance, position, refresh])

  useEffect(() => {
    if (positionInfo && position) {
      (async () => {
        // @ts-ignore
        const positionValues = await managerData.contract.getPositionValueInfo(position, positionInfo[0].toNumber().toString())
        if (positionValues) {

          // @ts-ignore
          setPositionValuesInfo(new Map(Object.entries(positionValues)))
        }
      })();
    }
  }, [positionInfo, managerData, position, refresh, account])

  useEffect(() => {
    if (positionValuesInfo) {
      const val = () => {
        // @ts-ignore
        // return  formatTheValue(positionValuesInfo.get('collateralValue'), tokens.WBTC.decimals) + formatTheValue(positionValuesInfo.get('interestGuaranteed'), tokens.WBTC.decimals) - formatTheValue(positionValuesInfo.get('accObligation'), tokens.WBTC.decimals)
        return positionValuesInfo.get('collateralValue').add(positionValuesInfo.get('interestGuaranteed')).sub(positionValuesInfo.get('accObligation'))
      }
      // @ts-ignore
      setTotalCollateralValue(val())
    }
    return () => {
      setTotalCollateralValue(null)
    }
  }, [positionValuesInfo, account])

  useEffect(() => {
    if (managerData && positionInfo && positionValuesInfo) {
      const contract = getContract(managerData.signer, tokens[managerData.token].blockUpdater.abi, tokens[managerData.token].blockUpdater.address[chainId]);
      if (contract) {
        // @ts-ignore
        contract.estimatedDailyReward(positionInfo[0]).then(res => {
          if (res.toNumber()) {
            // @ts-ignore
            setEstimatedDailySingleDebit(res / positionInfo[0].div(BigNumber.from(10).pow(tokens[managerData.token].decimals)))
            // @ts-ignore
            setGuaranteeDays(positionValuesInfo.get('interestGuaranteed').div(res))
          }
        })
      }
    }

    return () => {
      setGuaranteeDays(null);
      setEstimatedDailySingleDebit(null)
    }
  }, [chainId, managerData, positionValuesInfo, account, positionInfo])

  return <PositionContext.Provider value={{
    walletTokenBalance,
    avatarAssetList,
    positionValuesInfo,
    totalCollateralValue,
    guaranteeDays,
    estimatedDailySingleDebit,
    positionInfo,
    position,
    refreshStatus: refresh,
    refresh: setRefresh,
    onComputedCollateralRatio
  }}>{children}</PositionContext.Provider>
}

export {PositionContext, PositionContextProvider}
