import {useCallback, useEffect, useState} from "react";
import {useWeb3React} from "web3-react-core";
import {getContract, getSigner} from "utils/web3React";
// eslint-disable-next-line import/no-named-as-default
import tokens from 'config/constants/tokens'
import ERC20 from 'config/abi/erc20.json'
import {Web3Provider} from "@ethersproject/providers";
import contracts from "../config/constants/contracts";
import {kovanAddresses} from "../config/constants/types";
import {useGetCollateralManageState} from "../state/hooks";
import {useAppSelector} from "../state";


const {positionManagerFBTC20} = contracts;


export function useManagerList() {
  const [managerList, setManagerList] = useState([]);
  const {active, account, library, chainId} = useWeb3React();
  const generateData = useCallback(() => {
    const list = [];
    if (active && account) {
      // find manager contracts
      Object.keys(contracts).forEach(it => {
        if (it.includes('Manager')) {
          console.log('change account>>>', account)
          const signer = getSigner(library, account);
          const data = {
            ...contracts[it],
            signer,
            contract: getContract(signer, contracts[it].abi, contracts[it].address[chainId])
          }
          list.push(data)
        }
      })
    }
    setManagerList(list)
  }, [account, active, chainId, library])
  useEffect(() => {
    generateData();
    return ()=>{
      setManagerList([])
    }
  }, [generateData, account])
  return managerList
}

export function useManagerData() {
  const managerList = useManagerList();
  const currentManageTicker = useAppSelector(state => state.collateralManage.currentManageTicker)
  // eslint-disable-next-line consistent-return
  return useCallback(() => {
    if (managerList && currentManageTicker) {
      const _data = managerList.filter(it => {
        return it.token === currentManageTicker.Ticker
      });
      if (_data.length) {
        return _data;
      }
    }
  }, [currentManageTicker, managerList])
}

export function useGetPosition(active, account, library) {
  // eslint-disable-next-line consistent-return
  const callback = useCallback(async (abi, managerAddress) => {
    if (active && library) {
      const signer = getSigner(library, account);
      const contract = getContract(signer, abi, managerAddress);
      if (contract) {
        return contract.positions(account);
      }

    }
  }, [active, library, account]);

  return {getPosition: callback};
}

export function useCreatePosition(abi, managerAddress) {
  const {account, library, active} = useWeb3React();
  const [tx, setTx] = useState<any>();
  // eslint-disable-next-line consistent-return
  const callback = useCallback(async () => {
    if (active) {
      const signer = getSigner(library, account)
      const contract = getContract(signer, abi, managerAddress);
      if (contract) {
        setTx('waiting')
        return contract.createPosition()
      }
    }
  }, [active, library, account, abi, managerAddress])

  return {createPosition: callback, tx}
}


export function useGetTokenBalance(library, active, chainId) {
  // eslint-disable-next-line consistent-return
  return useCallback(async (address) => {
    if (active && address) {
      const signer = getSigner(library, address)
      // const balance = {};
      const balance = new Map();
      // eslint-disable-next-line no-restricted-syntax
      for (const token in tokens) {
        // eslint-disable-next-line no-continue
        if (token === 'F(BTC,20)') continue;
        const contract = getContract(signer, ERC20, tokens[token].address[chainId]);
        if (contract) {
          try {
            // eslint-disable-next-line no-await-in-loop
            const _balance = await contract.balanceOf(address);
            // eslint-disable-next-line no-prototype-builtins
            if (balance) {

              balance.set(token, _balance._hex)
              if (balance.has(token)) {
                // eslint-disable-next-line operator-assignment
                balance.set(token, Number(balance.get(token)) / (10 ** tokens[token].decimals))
              }
            }
          } catch (e) {
            // eslint-disable-next-line no-console
            console.log(e)
          }
        }
      }
      return balance
    }
  }, [active, library, chainId])
}

export function useGetGuaranteesInfo(managerData, position, active) {
  const [positionInfo, setGuaranteeInfo] = useState()

  useEffect(() => {
    (async () => {
      if (active && managerData) {
        try {
          const info = await managerData.contract.positionInfo(position)
          if (info) {
            setGuaranteeInfo(info);
          }
        } catch (e) {
          //
        }
      }
    })();
    return ()=>{
      setGuaranteeInfo(null)
    }
  }, [active, managerData, position])

  return positionInfo
}

/**
 * @desc Add asset from my wallet to guarantee pool
 * */
export function useAddGuarantee(abi, managerAddress) {
  const {library, active, account} = useWeb3React();
  // eslint-disable-next-line consistent-return
  const callback = useCallback(async (position, amount) => {
    if (active) {
      const signer = getSigner(library, account)
      const contract = getContract(signer, abi, managerAddress);
      console.log('Contract>>>', contract)
      if (contract) {
        return contract.addGuarantee(position, amount)
      }
    }
  }, [active, library, account, abi, managerAddress]);
  return {addGuarantee: callback}
}

/**
 * @desc Withdraw asset from guarantee to my wallet
 * */
export function useRemoveGuarantee(abi, managerAddress, returnAddress?) {
  const {library, active, account} = useWeb3React();
  // eslint-disable-next-line consistent-return
  const callback = useCallback(async (position, amount) => {
    if (active) {
      const signer = getSigner(library, account)
      const contract = getContract(signer, abi, managerAddress);
      if (contract) {
        return contract.moveGuarantee(position, returnAddress || account, amount)
      }
    }
  }, [active, library, account, abi, managerAddress, returnAddress]);
  return {removeGuarantee: callback}
}

/**
 * @desc transfer
 * */
export function useTransfer() {
  const {library} = useWeb3React()
  // eslint-disable-next-line consistent-return
  return useCallback(async (owner, from, to, amount) => {
    if (library) {
      const signer = getSigner(library, owner);
      const contract = getContract(signer, ERC20, from);
      if (contract) {
        return contract.transfer(to, amount)
      }
    }
  }, [library])
}


/**
 * @dec Get the specified token balance
 * */
export function useGetSpecifiedTokenBalance(library: Web3Provider, active: boolean, chainId: number) {
  // eslint-disable-next-line consistent-return
  return useCallback(async (signerAddress, tokenSymbol) => {
    if (active && signerAddress) {
      const signer = getSigner(library, signerAddress)
      const contract = getContract(signer, ERC20, tokens[tokenSymbol].address[chainId]);
      if (contract) {
        return contract.balanceOf(signerAddress);
      }
    }
  }, [active, library, chainId])
}

