import {useCallback} from 'react'
import contracts from 'config/constants/contracts'
import { kovanAddresses } from 'config/constants/types'
import { BigNumber } from 'ethers'
import { getContract, getSigner } from 'utils/web3React'

export function useFTC20COWBOYOracle(account , library) {
  return useCallback(
    // eslint-disable-next-line consistent-return
    async (address: kovanAddresses, amount: number | BigNumber | string) => {
      if(account) {
        const signer = await getSigner(library, account);
        const Contract = await getContract(signer, contracts.uniswapFBTC20WBTCoracle.abi, kovanAddresses.uniswapFBTC20WBTCoracle)
        if (Contract) {
          return Contract.consultCurrent(address, amount)
        }
      }
    },
    [account, library]
  )
}

export function useWBTCUSDTOrcale(account , library) {
  return useCallback(
    // eslint-disable-next-line consistent-return
    async (address: string, amount: number | BigNumber | string) => {
      if(account && library) {
        const signer = getSigner(library, account);

        const Contract = getContract(signer, contracts.uniswapUSDTWBTCoracle.abi, kovanAddresses.uniswapUSDTWBTCoracle)

        if (Contract) {
          return Contract.consultCurrent(address, amount)
        }
      }
    },
    [account, library],
  )
}
