import React, {useCallback, useState} from 'react';
import {useHistory} from "react-router-dom";
import {useWeb3React} from "web3-react-core";
import {Button, Stack} from '@mui/material';
import {styled} from '@mui/material/styles'
import ButtonUnstyled, {buttonUnstyledClasses, ButtonUnstyledProps,} from '@mui/base/ButtonUnstyled';
import {CollateralProps, OpenStatus} from "config/constants/types";
import contracts from 'config/constants/contracts'
import {useAppDispatch} from "state";
import {useGetCollateralManageState, useUpdataCurrentManagerAddress, useUpdataCurrentManageTicker} from "state/hooks";
import {useCreatePosition} from "hooks/useManager";
import useToast from "hooks/useToast";
import {Button as ButtonStyled} from 'components/uikit/components/Button'
// eslint-disable-next-line import/named
import {ShareIcon} from 'components/uikit/components/Svg'
import {ContractTransaction} from "ethers";
import {FlexWrap} from "components/util";


const blue = {
  500: '#007FFF',
  600: '#0072E5',
  700: '#0059B2',
  color: '#2AA1FF'
};

const CustomButtonRoot = styled(Button)`
  //display: block;
  font-family: 'PingFang SC';
  font-size: 0.875rem;
  border-radius: 8px;
  transition: all 150ms ease;
  cursor: pointer;
  border: none;
  padding: 0;
  color: ${blue.color};
  text-align: left;

  &:hover {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.active} {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.focusVisible} {
    //box-shadow: 0 4px 20px 0 rgba(61, 71, 82, 0.1), 0 0 0 5px rgba(0, 127, 255, 0.5);
    outline: none;
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const AlertButton = styled(ButtonStyled)`
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.043);
  border-radius: 14px;
  background: linear-gradient(270deg, #7AE9F1 0%, #61C3F7 100%);
  border-radius: 14px;
  font-size: 14px;
  height: 32px;
  line-height: 32px;
  color: #000;
  font-weight: normal;
`

function CustomButton(props: ButtonUnstyledProps) {
  return <ButtonUnstyled {...props} component={CustomButtonRoot}/>;
}




function Action({row}) {
  // eslint-disable-next-line camelcase
  const {positionManagerFBTC20} = contracts;
  const {status, avatar, ticker} = row;
  const [openText, setOpenText] = useState<string>()
  const [txDatas, setTxInfo] = useState<ContractTransaction>()
  const {chainId} = useWeb3React();
  const {toastInfo, toastError, toastSuccess} = useToast();
  const {changeManagerAddress} = useUpdataCurrentManagerAddress();
  // @ts-ignore
  // const {getPosition} = useGetPosition(positionManagerFBTC20.abi, positionManagerFBTC20.address[chainId]);
  const {createPosition} = useCreatePosition(positionManagerFBTC20.abi, positionManagerFBTC20.address[chainId ])
  const history = useHistory();
  const dispatch = useAppDispatch();
  const collateralList = useGetCollateralManageState('collateralList');
  const [list, setList] = useState<CollateralProps[]>(collateralList);



  const onOpen = useCallback(async () => {
    try {
      const txInfo = await createPosition()
      if (txInfo!==undefined && txInfo.hash) {
        setTxInfo(txInfo)
        setOpenText('waiting');
        const info = await txInfo.wait();
        toastInfo('Submitted', (
          <>
            <p style={{marginBottom: 24}}>Waiting for confirmation</p>
            <FlexWrap style={{flexDirection: 'row-reverse'}}>
              <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txInfo.hash}`)}}>View on Explorer</AlertButton>
            </FlexWrap>
          </>
        ))
     /*   if (info) {
          const position = await getPosition();
          if (position !== undefined && position.toString()) {
            setList(collateralList.map(it => {
              if (it.ticker === row.ticker) {
                return {...it, status: OpenStatus.opened, avatar: position}
              }
              return it
            }))
            dispatch(updateCollateralList({collateralList: list}));
            toastSuccess('Submitted', (
              <>
                <p style={{marginBottom: 24}}>{ticker} collateral account was opened.
                  You can add collateral first and then go to mint the token.</p>
                <FlexWrap style={{flexDirection: 'row-reverse'}}>
                  <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/address/${position}`)}}>View on Explorer</AlertButton>
                </FlexWrap>
              </>
            ))
            setOpenText('')
          }

        } */
      }
    } catch (error) {
      if(error) {
        toastError('Failed', error.message)
      }
    }


  }, [createPosition, toastInfo, toastError])

  return (
    <div>

      <Stack direction="row" spacing={2}>
        {
          openText === 'waiting' ?
            <CustomButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txDatas.hash}`)}}>waiting</CustomButton>:
            <CustomButton onClick={onOpen} disabled={Number(avatar) !== 0 || openText === 'waiting'}>{
              // eslint-disable-next-line no-nested-ternary
              status === OpenStatus.notOpen ? 'open' : status === OpenStatus.waiting ? 'waiting' : 'opened'
            }</CustomButton>
        }
        <CustomButton onClick={() => {
          changeManagerAddress({currentManagerAddress: row.managerAddress});
          history.push('/collateral/manage')
        }} disabled={Number(avatar) === 0}>
          Manage
        </CustomButton>
        <CustomButton onClick={() => {
          window.open(`https://kovan.etherscan.io/address/${avatar}`)
        }} disabled={Number(avatar) === 0} >
          <ShareIcon color={Number(avatar)? blue.color : '#ccc'} />
        </CustomButton>
      </Stack>
    </div>
  );
}

export default Action
