import React, {useCallback, useEffect, useState} from 'react';
import {Typography} from '@mui/material/';
import Wallet from 'components/uikit/widgets/Menu/icons/Wallet'
import Methods from 'components/Methods'
import {useWeb3React} from "web3-react-core";
import {
  Wrapper,
  Title,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Approve,
  DataWrap,
  IconWrap,
  icon, FlexWrap, AlertButton
} from "components/util";
import {BigNumber} from "@ethersproject/bignumber";
import usePanel from "hooks/usePanel";
// eslint-disable-next-line import/named
import tokens, { poolData } from "config/constants/tokens";
import useToast from "hooks/useToast";
import {useRemoveGuarantee, useTransfer} from "hooks/useManager";
import contracts from "config/constants/contracts";
import {useGetCollateralManageState} from "state/hooks";
import {formatWBTC} from 'utils/formatTheValue'

const options = [
  ...poolData
]

export default function CustomizedAccordions(props) {
  const {active, chainId, account} = useWeb3React()
  const {managerInfo, guaranteeBalance} = props;
  const {expanded, handleChange} = usePanel();
  const [walletWbtcBalance,setWalletBtcBalance] = useState()
  // const {getTokenBalance} = useGetTokenBalance(account);
  // eslint-disable-next-line camelcase
  const {positionManagerFBTC20} = contracts;
  const [m1LfVal,setM1LfVal] = useState<number>()
  const [m2LfVal,setM2LfVal] = useState<number>()
  const transfer = useTransfer();
  const {toastError, toastSuccess, toastInfo} = useToast();
  const {removeGuarantee} = useRemoveGuarantee(positionManagerFBTC20.abi, positionManagerFBTC20.address[chainId])
  const managerApproveStatus = useGetCollateralManageState('FBTC20ApproveStatus')

  const onGuaranteeCurrencyToWallet = useCallback(async ()=>{
    if (active) {
      try {
        const txInfo = await removeGuarantee(managerInfo.avatar, BigNumber.from(10).pow(tokens.WBTC.decimals).mul(m1LfVal));
        if (txInfo) {
          toastInfo('Submitted ', (
            <>
              <p style={{marginBottom: 24}}>Waiting for confirmation</p>
              <FlexWrap style={{flexDirection: 'row-reverse'}}>
                <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txInfo.hash}`)}}>View on Explorer</AlertButton>
              </FlexWrap>
            </>
          ))
          const result = await txInfo.wait();
          if (result) {
            toastSuccess('Submitted', (
              <>
                <p style={{marginBottom: 24}} >{managerInfo.avatar} collateral account was opened.
                  You can add collateral first and then go to mint the token.</p>
                <FlexWrap style={{flexDirection: 'row-reverse'}}>
                  <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/address/${managerInfo.avatar}`)}}>View on Explorer</AlertButton>
                </FlexWrap>
              </>
            ))
          }
        }
      } catch (e) {
        toastError('Failed', e.message)
      }
    }
  }, [active, m1LfVal, removeGuarantee, managerInfo, toastInfo, toastSuccess, toastError])

  const onTransfer = useCallback(async ()=>{
    if (active && account) {
      await transfer(managerInfo.avatar,  tokens.WBTC.address[chainId], account,BigNumber.from(10).pow(tokens.WBTC.decimals).mul(m2LfVal))
    }
  } , [account, active, chainId, m2LfVal, managerInfo, transfer])

  const onInput = (e)=>{
    setM1LfVal(e.target.value);
  }
  const onMax = ()=>{
    // @ts-ignore
    setM1LfVal(formatWBTC(guaranteeBalance))
  }
  /**
   * getWalletTokenBalance
   * */
/*  useEffect(()=>{
    if (active && !walletWbtcBalance && account) {
      getTokenBalance().then(res=>{
        if (res) {
          // @ts-ignore
          setWalletBtcBalance(res.WBTC)
        }
      })
    }
  }, [active,account, getTokenBalance, walletWbtcBalance, toastInfo]) */
  // @ts-ignore
  return (
    <Wrapper>
      <Title>Withdraw Collateral</Title>
      <Accordion expanded={expanded === 'panel13'} onChange={handleChange('panel13')}>
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography>Method 1. Withdraw asset from avatar to my wallet</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Methods
            SelectIcon={icon(IconWrap, 'A')} options={options} rightBalance={2.11} rightOptions={options}
            rightSelectedValue={42} rightSymbol="WBTC" selectValue={42} selectedBalance={100} selectedSymbol="WBTC"
            leftInputValue={m2LfVal}
            rightInputValue={m2LfVal}
            selectInput={(e)=>{
              // @ts-ignore
              setM2LfVal(e.target.value);
            }}
            RightIcon={Wallet}/>
          <DataWrap>
            <div className="ratio">
              <p>Guarantee Ratio</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
            <div className="days">
              <p>Guarantee Ratio</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
          </DataWrap>
          <Approve style={{boxShadow: 'none'}} onClick={onTransfer}>{managerApproveStatus?'Submit':'Approve'}</Approve>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel14'} onChange={handleChange('panel14')}>
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography>Method 2. Withdraw asset from guarantee to my wallet</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Methods
            selectInput={onInput}
            onMax={onMax}
            SelectIcon={icon(IconWrap,'G')} options={options} rightBalance={walletWbtcBalance} rightOptions={options}
            rightSelectedValue={42} rightSymbol="WBTC" selectValue={42} selectedBalance={100} selectedSymbol="WBTC"
            leftBalance={guaranteeBalance?formatWBTC(guaranteeBalance) : 0}
            leftInputValue={m1LfVal}
            rightInputValue={m1LfVal}
            RightIcon={Wallet}/>
          <DataWrap>
            <div className="ratio">
              <p>Guarantee Ratio</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
            <div className="days">
              <p>Guarantee Ratio</p>
              <p>
                <span className="unit">N/A</span>
                <span className="icon">——&gt;</span>
                <span className="icon">+∞</span>
              </p>
            </div>
          </DataWrap>
          <Approve style={{boxShadow: 'none'}} onClick={onGuaranteeCurrencyToWallet}>{managerApproveStatus?'Submit':'Approve'}</Approve>
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  );
}
