import React, {useContext, useEffect, useState} from 'react'
import {useWeb3React} from "web3-react-core";
// eslint-disable-next-line import/named
import {AntTabs, AntTab} from 'components/Tabs/index'
import {styled} from '@mui/material/styles'
import {Grid} from '@mui/material'
import {ManagerContext, ManagerContextProvider} from "contexts/ManagerContext";
import {PositionContextProvider} from "contexts/PositionContext";
import MyCollateral from 'components/MyCollateral'
import Mint from './components/Mint'
import Neutralize from './components/Neutralize'

const Title = styled(AntTab)`
  font-size: 32px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.5);
  padding-left: 0;
  margin-right: 24px;

  &.Mui-selected {
    color: #000;
  }
`
const Wrapper = styled(AntTabs)`
  margin: 0;
  margin-bottom: 18px;
`

function MintPage() {
  // @ts-ignore
  const {managerData} = useContext(ManagerContext)
  const {active, account} = useWeb3React()
  const [value, setValue] = useState(0)
  const [position, setPosition] = useState();
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue)
  }

  useEffect(() => {
    if (active && account && managerData) {
      managerData.contract.positions(account).then(res => {
        if (res) {
          console.log('postions>>>', res)
          setPosition(res);
        }
      }).catch(res=>{
        // @ts-ignore
        setPosition('0x00000000000000000000000000000000');
      })
    }
    return ()=>{
      setPosition(null);
    }
  }, [account, active, managerData])


  return (

    <div style={{paddingRight: 44}}>

      <Wrapper value={value} onChange={handleChange}>
        <Title label="Mint"/>
        <Title label="Neutralize"/>
      </Wrapper>
      <PositionContextProvider position={position}>
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <Mint hidden={value !== 0}/>
            <Neutralize hidden={value !== 1}/>
          </Grid>
          <Grid item xs={4}>
            <MyCollateral/>
          </Grid>
        </Grid>
      </PositionContextProvider>

    </div>

  )
}

export default MintPage
