import * as React from 'react';
import {Input} from '@mui/material'
import SelectUnstyled, {
  SelectUnstyledProps,
  selectUnstyledClasses,
} from '@mui/base/SelectUnstyled';
import OptionUnstyled, {optionUnstyledClasses} from '@mui/base/OptionUnstyled';
import PopperUnstyled from '@mui/base/PopperUnstyled';
import {styled} from '@mui/system';
import down from 'assets/image/down.png'
import {useState} from "react";
// eslint-disable-next-line import/named
import {PoolProps} from "config/constants/types";
import { BG_COMMON_COLOR } from 'components/uikit/widgets/Menu/config';
import {useWeb3React} from "web3-react-core";

const blue = {
  100: '#DAECFF',
  200: '#99CCF3',
  400: '#3399FF',
  500: '#007FFF',
  600: '#0072E5',
  900: '#003A75',
};

const grey = {
  100: '#E7EBF0',
  200: '#E0E3E7',
  300: '#CDD2D7',
  400: '#B2BAC2',
  500: '#A0AAB4',
  600: '#6F7E8C',
  700: '#3E5060',
  800: '#2D3843',
  900: '#1A2027',
};

const StyledButton = styled('button')(
  ({theme}) => `
  position: relative;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  box-sizing: border-box;
  width: 214px;
  background: ${BG_COMMON_COLOR};
  border: 1px solid rgba(207, 216, 226, 0.5);
  border-radius: 0.75em;
  margin: 0.5em 0;
  padding: 10px;
  text-align: left;
  line-height: 1.5;
  color: ${theme.palette.mode === 'dark' ? grey[300] : grey[900]};

  &:hover {
    background: ${theme.palette.mode === 'dark' ? '' : grey[100]};
  }

  &.${selectUnstyledClasses.focusVisible} {
    outline: 3px solid ${theme.palette.mode === 'dark' ? blue[600] : blue[100]};
  }

  &.${selectUnstyledClasses.expanded} {
    &::after {
      transform:  translateY(-50%) rotate(180deg) ; 
    }
  }
  &.${selectUnstyledClasses.disabled} {
    background: transparent !important;
    &.select_custom{
       background: ${BG_COMMON_COLOR};
    }
    &::after {
      display:none;
    }
  }

  &::after {
    content: '';
    position: absolute;
    right: 10px;
    top: 50%;
    display: block;
    width: 7px;
    height: 5px;
    transition:transform 0.3s;
    transform: translateY(-50%);
    background: url(${down});
  }
  `,
);

const StyledListbox = styled('ul')(
  ({theme}) => `
  width: 214px;
  box-sizing: border-box;
  padding: 9px 0;
  margin: 10px 0;
  font-size: 0.875rem;
  font-family: IBM Plex Sans, sans-serif;
  transition: .3s;
  background: rgba(255, 255, 255, 1);
  border: 1px solid ${theme.palette.mode === 'dark' ? grey[800] : grey[300]};
  border-radius: 0.75em;
  color: ${theme.palette.mode === 'dark' ? grey[300] : grey[900]};
  overflow: auto;
  outline: 0px;
  `,
);

const StyledOption = styled(OptionUnstyled)(
  ({theme}) => `
  list-style: none;
  padding: 8px;
  cursor: default;
  display: flex;
  align-items: center;
  &:last-of-type {
    border-bottom: none;
  }
  color: #000000;
  font-weight: bold;
  transition: .3s;
  &.${optionUnstyledClasses.selected} {
    background: rgba(255, 255, 255, 0.41);
    color: ${theme.palette.mode === 'dark' ? blue[100] : blue[900]};
  }

  &.${optionUnstyledClasses.highlighted} {
   background: rgba(255, 255, 255, 0.41);
    color: ${theme.palette.mode === 'dark' ? grey[300] : grey[900]};
  }

  &.${optionUnstyledClasses.highlighted}.${optionUnstyledClasses.selected} {
    background:  rgba(110, 214, 243, 0.23);
  }

  &.${optionUnstyledClasses.disabled} {
    color: ${theme.palette.mode === 'dark' ? grey[700] : grey[400]};
  }

  &:hover:not(.${optionUnstyledClasses.disabled}) {
    background-color: ${theme.palette.mode === 'dark' ? grey[800] : grey[100]};
    color: ${theme.palette.mode === 'dark' ? grey[300] : grey[900]};
  }
  `,
);


const StyledPopper = styled(PopperUnstyled)`
  z-index: 1;
`;
const Group = styled('div')`
  display: flex;
  justify-content: space-between;
  width: 260px;
  padding: 12px;
  border: 1px solid #CFD8E280;
  background-color: ${BG_COMMON_COLOR};
  border-radius: 11px;
  box-sizing: border-box;
  .MuiSelectUnstyled-root {
    margin: 0;
    padding: 2.5px 0;
    //margin-bottom: ;
    background-color: transparent;
    border: none;
    width: 50%;
    &:before{
      position: absolute;
      right: 0;
      top: 0;
      content: '';
      height: 100%;
      width: 1px;
      background-color: rgba(207, 216, 226, 0.5);
    }
    + div.MuiSelectUnstyled-popper {
      padding: 10px 0;
      margin-left:-13px !important;
      
    }
    
  }
  div.MuiInput-root{
    padding: 0;
    width: 50%;
    padding-left: 10px;
    color: #000;
    font-weight: bold;
    input::-webkit-inner-spin-button {
      display: none;
    }
  }
`


const CustomSelect = React.forwardRef(function CustomSelect<TValue>(
  props: SelectUnstyledProps<TValue>,
  ref: React.ForwardedRef<HTMLUListElement>,
) {
  const components: SelectUnstyledProps<TValue>['components'] = {
    Root: StyledButton,
    Listbox: StyledListbox,
    Popper: StyledPopper,
    ...props.components,
  };

  return <SelectUnstyled {...props} ref={ref} components={components}/>;
}) as <TValue>(
  props: SelectUnstyledProps<TValue> & React.RefAttributes<HTMLUListElement>,
) => JSX.Element;

export interface Props<T> extends SelectUnstyledProps<T>{
  options: PoolProps[]
  isGroup?: boolean
  groupStyle?: any
  selectDisabled?: boolean
  allDisabled?: boolean
  selectValue?: T | null
  actionValue?: number
  selectInput?: (arg1:React.SyntheticEvent)=> void
  type?: string
  onBlur?: any
}

export default function UnstyledSelectSimple<T>(props: Props<T>) {
  const {options,type, isGroup, groupStyle, selectDisabled, allDisabled, selectValue, onChange, actionValue, selectInput,onBlur} = props;
  const [opt,] = useState<PoolProps[]>(options)
  const {chainId} = useWeb3React();

  if (type === 'network') {
    return <CustomSelect disabled={selectDisabled || allDisabled} value={selectValue} onChange={onChange}>
      {opt.map(it=>(
        <StyledOption value={it.chainId} key={it.chainId}>
                <span style={{display:'flex', alignItems:'center'}}>
                <span style={{display: "inline-block",marginRight:12, width: 22, height: 22, borderRadius: "50%", background: "#000"}}/>
                <span style={{lineHeight:1}}>{it.networkName}</span>
                </span>
        </StyledOption>
      ))}
    </CustomSelect>
  }
    return (
      <>
        {
          !isGroup? <CustomSelect className="select_custom" disabled={selectDisabled || allDisabled} value={selectValue} onChange={onChange}>
            {opt ? options.map(it=>(
              <StyledOption value={it.token} key={it.address[chainId]}>
                <span style={{display:'flex', alignItems:'center'}}>
                <span style={{display: "inline-block",marginRight:12, width: 22, height: 22, borderRadius: "50%", background: "#000"}}/>
                <span style={{lineHeight:1}}>{it.token}</span>
                </span>
              </StyledOption>
            )) : null}
            {/* eslint-disable-next-line react/destructuring-assignment */}
          </CustomSelect>: <Group style={groupStyle}>
            <CustomSelect disabled={selectDisabled || allDisabled} value={selectValue}  onChange={onChange}>
              {options.map(it=>(
                <StyledOption value={it.token} key={it.address}>
                <span style={{display:'flex', alignItems:'center'}}>
                <span style={{display: "inline-block",marginRight:12, width: 22, height: 22, borderRadius: "50%", background: "#000"}}/>
                <span style={{lineHeight:1}}>{it.token}</span>
                </span>
                </StyledOption>
              ))}

            </CustomSelect>
            <Input dir='rtl' id="mediaVal" onBlur={onBlur} disabled={allDisabled} value={actionValue} disableUnderline type="number" onInput={(e)=>{
              if (selectInput) {
                selectInput(e)
              }
            }} />
          </Group>
        }
      </>

    );


}
