import {useCallback} from "react";
import {useAppDispatch, useAppSelector} from "./index";
import {
  fetchCollateralList,
  updateCurrentTicker,
  updateFBTC20ApproveStatus,
  updateRatio,
  updateCurrentManagerAddress
} from "./CollateralManage/actions";
import {updataHoldingsWallet} from "./Hodlings/action";

export function useGetState(_state) {
  return useAppSelector(state=> state[_state] )
}

export function useGetCollateralManageState(key) {
  return useAppSelector(state=> state.collateralManage[key] )
}

export function useGetHoldingsState(key) {
  return useAppSelector(state=> state.holdings[key] )
}

export function useHoldingWallet() {
  return useAppSelector(state=>state.holdings.holdingsWallet)

}


export function useUpdateRatio() {
  const ratio = useGetCollateralManageState('ratio');

  const dispatch = useAppDispatch();

  const callBack = useCallback((data: {ratio: number})=>{
    dispatch(updateRatio(data))
  },[dispatch]);

  return {ratio, handleUpdataRatio: callBack}
}

export function useCollateralList() {
  const collateralList = useAppSelector(state=>state.collateralManage.collateralList)
  const dispatch = useAppDispatch();
  const callback = useCallback(async (data)=>{
    dispatch(fetchCollateralList(data));
  },[dispatch])
  return {collateralList, onUpdateCollateralList:callback }
}

export function useLiquidationList() {
  return  useAppSelector(state=>state.liquidation.list)
}

export function useUpdataCurrentManageTicker(){
  const dispatch = useAppDispatch();

  const callback = useCallback((data)=>{
    dispatch(updateCurrentTicker(data))
  },[dispatch])
  return {onManage: callback}
}



export function useUpdataCurrentManagerAddress(){
  const dispatch = useAppDispatch();

  const callback = useCallback((data)=>{
    dispatch(updateCurrentManagerAddress(data))
  },[dispatch])
  return {changeManagerAddress: callback}
}

export function useUpdataFBTC20ApproveStatus(){
  const dispatch = useAppDispatch();

  const callback = useCallback((data)=>{
    dispatch(updateFBTC20ApproveStatus(data))
  },[dispatch])
  return {onFBTC20ApproveStatus: callback}
}


export function useUpdataHoldingWallet(){
  const dispatch = useAppDispatch();
  return useCallback(async (data)=>{
    dispatch(updataHoldingsWallet(data));
    return true;
  },[dispatch])
}
