import React, {useCallback, useContext, useEffect, useState} from 'react';
import {useWeb3React} from "web3-react-core";
import {useHistory} from "react-router-dom";
import {styled} from '@mui/material/styles'
import {Grid, List, ListItem, Divider} from '@mui/material';
import tokens from 'config/constants/tokens';
import Slide from "components/Slide";
import Select from 'components/Select';
import Card from "components/Card";
import {ManagerContext} from 'contexts/ManagerContext';
import {PositionContext, PositionContextProvider} from 'contexts/PositionContext';
import {IconWrap, FlexWrap} from "components/util";
import {useGetGuaranteesInfo, useGetTokenBalance, useManagerList} from "hooks/useManager";
import CopyToClipboard from "components/uikit/widgets/WalletModal/CopyToClipboard";
import {useAppSelector} from "state";
import {useGetCollateralManageState, useUpdataCurrentManagerAddress} from "state/hooks";
import {formatWBTC} from 'utils/formatTheValue'
import Title from "./Title";
import BeforeMintAction from "./BeforeMintAction";
import WithdrawCollateral from './WithdrawCollateral';

export const SubTitle = styled('p')`
  font-family: Heebo;
  font-size: 14px;
  line-height: 21px;
  color: rgba(0, 0, 0, 0.5);
`
const ScrollWrapper = styled('div')`
  height: calc(100vh - 189px);
  padding-bottom: 100px;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
  }
`

const Box = styled('div')`
  box-sizing: border-box;
  margin-top: 26px;
  padding-right: 44px;
`
const CardWrap = styled(Card)`
  position: relative;
  padding: 23px 0;
  display: flex;
  margin-bottom: 22px;
`
const TipPanel = styled('div')`
  width: 50%;
  padding: 0 30px 0 40px;
  display: flex;
  justify-content: space-between;

  &:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: .2px;
    height: 70%;
    background-color: #2DA3FF;

  }

  h2 {
    font-family: PingFang SC;
    font-weight: 600;
    font-size: 1rem;
    line-height: 22px;
    color: #000000;
  }

  .num {
    font-family: PingFang SC;
    font-weight: 600;
    font-size: 50px;
    line-height: 70px;
    color: #6ED6F3;
    margin: 5px 0 11px;

    sub {
      font-size: 20px;
      font-weight: 500;
    }
  }

  .tip {
    font-size: 12px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.5);
  }

  .num.guarantee {

    color: #EFD71E;
  }
`

function GuaranteeInfo() {
  // @ts-ignore
  const {positionInfo} = useContext(PositionContext);
  return (
    <CardWrap style={{padding: 20, justifyContent: "space-between"}}>
      <h3>Guarantee</h3>
      <span>{positionInfo ? formatWBTC(positionInfo[5]) : 0} WBTC</span>
    </CardWrap>
  )
}
function AvatarAssetList() {
  // @ts-ignore
  const {avatarAssetList} = useContext(PositionContext)
  if (!avatarAssetList) {
    return null
  }
  const temp = [];
  // eslint-disable-next-line @typescript-eslint/no-shadow
  let count = 0;
  if (avatarAssetList) {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars,guard-for-in,no-restricted-syntax
    for (const [token, balance] of avatarAssetList) {
      count++;
      temp.push((
        <React.StrictMode key={token}>
          <ListItem style={{
            fontSize: "14px",
            color: '#000000',
            display: 'flex',
            justifyContent: "space-between",
            padding: '16px 0',
          }}>
            <FlexWrap style={{alignItems: 'center'}}><img style={{marginRight: 10}} src="" width='20' height='20'
                                                          alt=""/>
              <span>{token}</span></FlexWrap>
            <FlexWrap style={{flexDirection: "column", alignItems: "flex-end"}}>
              <span>{balance}</span>
              <span style={{opacity: .5, fontSize: '12px', lineHeight: '20px'}}>{0}</span>
            </FlexWrap>
          </ListItem>
          <Divider hidden={count > avatarAssetList.size - 1}/>
        </React.StrictMode>
      ))
    }
  }

  return (
    <>
      {temp}
    </>
  )
}
function InfoPanel() {
  // @ts-ignore
  const {collateralParams} = useContext(ManagerContext)
  // @ts-ignore
  const {onComputedCollateralRatio, guaranteeDays} = useContext(PositionContext);
  const [days,setDays] = useState(0);
  const [ratioVal,setRatioVal] = useState(100);
  const [rangeDays,setRangeDays] = useState(0)
  const [rangeRatio,setRangeRatio] = useState(0)

  useEffect(()=>{
    if (guaranteeDays) {
      onComputedCollateralRatio(0,(ratio)=>{
        const _ratio = ratio*100;
        setRangeRatio((_ratio - 100) / 5);
        // @ts-ignore
        setRatioVal(Number(_ratio.toString().slice(0,_ratio.toString().indexOf('.')+3)));

        setRangeDays(guaranteeDays.toString() / 5);
        setDays(guaranteeDays.toString())
      })
    }

  }, [guaranteeDays, onComputedCollateralRatio])



  return (
    <CardWrap>
      <TipPanel>
        <div>
          <h2>Collateral Ratio</h2>
          <p className="num">{ratioVal || 0}<sub>%</sub></p>
          <p className="tip">Liquidation Ratio：{collateralParams ? collateralParams.get('minCollateralRate') / 100: 0} %</p>
        </div>
        <Slide style={{height: "100%"}} value={rangeRatio}/>
      </TipPanel>
      <TipPanel>
        <div>
          <h2>Guarantee</h2>
          <p className="num guarantee"><sub>≈</sub>{guaranteeDays?guaranteeDays.toString() :0}<sub>days</sub></p>
          <p className="tip">Min. Guarantee days：{collateralParams ? collateralParams.get('minGuaranteeDays').toString() : 0}</p>
        </div>
        <Slide style={{height: "100%"}} value={rangeDays}/>
      </TipPanel>
    </CardWrap>
  )
}


function Manage() {
  // @ts-ignore
  const {managerData} = useContext(ManagerContext);
  const {account, chainId} = useWeb3React();
  const managerList = useManagerList();
  const [guaranteeBalance,] = useState(0)
  const [currentManager, setCurrentManager] = useState(managerData)
  const [position, setPosition] = useState()
  const currentManageAddress = useGetCollateralManageState('currentManagerAddress')
  const history = useHistory();
  const {changeManagerAddress} = useUpdataCurrentManagerAddress()
  // change manager
  const onChange = useCallback((val) => {
    if (managerList) {
      const manager = managerList.filter(it => it.token === val)[0]
      setCurrentManager(manager);
    }
    changeManagerAddress({currentManagerAddress: managerList.filter(it => it.token === val)[0].address[chainId]})
  }, [managerList, changeManagerAddress, chainId])

  // emit update manager
  useEffect(() => {
    if (currentManager && account) {
      (async () => {
        try {
          const _position = await currentManager.contract.positions(account);
          console.log('_position>>>>', _position)
          if (Number(_position.toString())) {
            setPosition(_position)
          }else{
            history.replace('/collateral')
          }
        } catch (e) {
          history.replace('/collateral')
        }
      })();
      /* console.log(currentManager.address[chainId])
       // Whether you have the permission of the current manager
       if (!(currentManageAddress && currentManageAddress === currentManager.address[chainId] ) && Number(currentManager.avatar))  {
         history.replace('/collateral')
       } */
    }

  }, [currentManageAddress, history, currentManager, chainId, account])


  // init manager
  useEffect(() => {
    if (!currentManager && managerData) {
      // @ts-ignore
      setCurrentManager(managerData)
    }
  }, [currentManager, managerData, managerList])


  return (
    <>
      <Title>Collateral Management</Title>
      <SubTitle>Set the Collateral should above the Liq. ratio and the Min. Guarantee days</SubTitle>
      <Select isGroup={false} options={managerList} selectValue={currentManager ? currentManager.token : ''}
              onChange={onChange}/>

      <PositionContextProvider position={position}>
        <Box>
          <Grid container spacing={3}>
            <Grid item xs={8}>
              <ScrollWrapper>
                {/* collateral info */}
                <InfoPanel />
                <BeforeMintAction managerInfo={managerData}/>
                {/* <ImproveGuaranteeDays />
              <ImproveCollateralRatio />
              <TradingThroughAvatar />
              */}
                <WithdrawCollateral managerInfo={managerData} guaranteeBalance={guaranteeBalance}/>
                {/*  <GeneralTransfer /> */}


              </ScrollWrapper>
            </Grid>
            <Grid item xs={4}>
              <CardWrap style={{height: 240, padding: 20}}>placeholder...... </CardWrap>
              <GuaranteeInfo />
              <CardWrap style={{padding: 20, flexDirection: "column"}}>
                <h3 style={{marginBottom: 6}}>Avatar Asset List</h3>
                <FlexWrap style={{alignItems: "center"}}><IconWrap
                  style={{marginRight: 20}}>A</IconWrap><CopyToClipboard toCopy={account}><span
                  style={{color: "#000"}}>0x3f</span></CopyToClipboard></FlexWrap>
                <List component="nav" aria-label="main mailbox folders">
                  <ListItem style={{
                    fontSize: "12px",
                    padding: 0,
                    lineHeight: 2,
                    color: 'rgba(0, 0, 0, 0.5)',
                    display: 'flex',
                    justifyContent: "space-between"
                  }}>
                    <span>Ticker</span>
                    <span>Holdings - Avatar</span>
                  </ListItem>
                  <AvatarAssetList />
                </List>
              </CardWrap>
            </Grid>
          </Grid>
        </Box>
      </PositionContextProvider>
    </>
  );
}

export default Manage;
