import React, { useCallback } from 'react'
import { styled } from '@mui/material/styles'
import { ButtonBase } from '@mui/material'
import Select from 'components/Select'
import { tokensSymbol, kovanAddresses } from 'config/constants/types'
import { IconWrap } from 'components/util/index'

const SelectGroup = styled('div')`
  button.MuiSelectUnstyled-root {
    width: 200px;
  }

  div.MuiInput-root {
    width: calc(100% - 200px) !important;
  }
  div.topFunc{
    display: flex;
    align-items: center;
    padding: 5px 10px;
    span {
      margin-left: 20px ;
    }
  }
`

const Func = styled('div')`
  padding: 10px 20px;
  button {
    color: #2aa1ff;
  }
`

const MintData = [
  {
    token: tokensSymbol.FBTC20,
    symbol: tokensSymbol.FBTC20,
    address: {
      42: kovanAddresses.FBTC20,
    },
    balance: 0,
    chainId: 42,
  },
]

export default function Index() {
  const onMax = useCallback(() => {
    console.log('to Max')
  }, [])

  return (
    <SelectGroup>
      <div className='topFunc'><IconWrap>A</IconWrap><span>1.22345678 WBTC</span></div>
      <Select selectValue={tokensSymbol.FBTC20} groupStyle={{ width: '100%' }} options={MintData} isGroup />
      <Func dir="rtl">
        <ButtonBase onClick={onMax}>Max</ButtonBase>
      </Func>
    </SelectGroup>
  )
}
