import * as React from 'react';
import { styled } from '@mui/material/styles';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';

export interface StyledTabProps {
  label: string;
}


export interface StyledTabsProps {
  children?: React.ReactNode;
  value: number;
  onChange: (event: React.SyntheticEvent, newValue: number) => void;
}


export const AntTabs = styled((props: StyledTabsProps) => (
  <Tabs
    {...props}
    TabIndicatorProps={{ children: <span className="MuiTabs-indicatorSpan" /> }}
  />
))({
  margin: '40px auto 20px',
  '& .MuiTabs-indicator': {
    display: 'flex',
    justifyContent: 'center',
    height: 4,
    backgroundColor: 'transparent',
  },
  '& .MuiTabs-indicatorSpan': {
    maxWidth: 40,
    width: '100%',
    borderRadius: 5,
    background: 'linear-gradient(270deg, #7AE9F0 0%, #61C3F7 100%);',
  },
});

export const AntTab = styled((props: StyledTabProps) => <Tab disableRipple {...props} />)(
  ({ theme }) => ({
    textTransform: 'none',
    minWidth: 0,
    [theme.breakpoints.up('sm')]: {
      minWidth: 0,
    },
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(1),
    color: 'rgba(0, 0, 0, 0.85)',
    fontSize: '22px',
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:hover': {
      color: '#000000',
      opacity: 1,
    },
    '&.Mui-selected': {
      color: '#000000',
      fontWeight: theme.typography.fontWeightMedium,
    },
    '&.Mui-focusVisible': {
      backgroundColor: '#d1eaff',
    },
  }),
);



export default function CustomizedTabs(props) {
  const {panel1, panel2, tab, tabsStyle, tabStyle} = props;
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };


  return (
    <>
     {/* @ts-ignore */}
      <AntTabs style={tabsStyle} value={value} onChange={handleChange} aria-label="styled tabs example">
        {/* @ts-ignore */}
        {  tab.map(it=>( <AntTab style={tabStyle} key={it} label={it} /> ))  }
      </AntTabs>
      {
        // @ts-ignore
        // eslint-disable-next-line no-nested-ternary
        value === 0 ? panel1 : value === 1 ? panel2 : value === 2 ? 2 : 3
      }
    </>
  );
}
