import React, {useCallback} from 'react';
import {Button, Stack} from '@mui/material';
import {styled} from '@mui/material/styles'
import ButtonUnstyled, {buttonUnstyledClasses, ButtonUnstyledProps,} from '@mui/base/ButtonUnstyled';
import {HoldingsProps, kovanAddresses} from "config/constants/types";
import {Button as ButtonStyled} from 'components/uikit/components/Button'
import {useWeb3React} from "web3-react-core";
import interestBearingToken from 'config/abi/interestBearingToken.json'
import addAsset from "../../../utils/addAsset";
import {useClaimRewards} from "../../../hooks/useManager";

const blue = {
  500: '#007FFF',
  600: '#0072E5',
  700: '#0059B2',
  color: '#2AA1FF'
};

const CustomButtonRoot = styled(Button)`
  //display: block;
  font-family: 'PingFang SC';
  font-size: 0.875rem;
  border-radius: 8px;
  transition: all 150ms ease;
  cursor: pointer;
  border: none;
  padding: 0;
  color: ${blue.color};
  text-align: left;

  &:hover {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.active} {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.focusVisible} {
    //box-shadow: 0 4px 20px 0 rgba(61, 71, 82, 0.1), 0 0 0 5px rgba(0, 127, 255, 0.5);
    outline: none;
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

export const AlertButton = styled(ButtonStyled)`
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.043);
  border-radius: 14px;
  background: linear-gradient(270deg, #7AE9F1 0%, #61C3F7 100%);
  border-radius: 14px;
  font-size: 14px;
  height: 32px;
  line-height: 32px;
  color: #000;
  font-weight: normal;
`

function CustomButton(props: ButtonUnstyledProps) {
  return <ButtonUnstyled {...props} component={CustomButtonRoot}/>;
}


// eslint-disable-next-line @typescript-eslint/no-unused-vars
function Action({row}: { row: HoldingsProps }) {
  const {connector, active, chainId, library, account} = useWeb3React()
  const {ticker} = row;
  const addToMetaMask = useCallback(async ()=>{
    if (active && connector && ticker) {
      addAsset({connector, tokenSymbol:ticker , chainId})
    }
  }, [connector, active, chainId, ticker]);
  const claimReward = useClaimRewards(library, active, account)

  const onClaim = useCallback(async ()=>{
    if (active && account) {
      try {
        const info = await claimReward(kovanAddresses[ticker=== 'F(BTC,20)'? 'FBTC20':ticker ], interestBearingToken);
      } catch (e) {
        console.log(e);
      }
    }
  }, [active, account, claimReward, ticker])

  return (
    <div>

      <Stack direction="row" spacing={2}>
        <CustomButton onClick={onClaim}>Claim Rewards</CustomButton>
        <CustomButton onClick={()=>{window.open('https://app.uniswap.org/#/swap?exactField=input&exactField=output&inputCurrency=0xa91aE25794d4CAA73C8A521FfDbbfe2c1C0da172&outputCurrency=0x7e11aac35bd6159fb4358879e8fd8cbb92a22268&chain=kovan')}}>Uniswap Trade</CustomButton>
        <CustomButton onClick={addToMetaMask}>add to Metamask</CustomButton>
      </Stack>
    </div>
  );
}

export default Action
