import * as React from 'react';
import {styled} from '@mui/material/styles';
import {
  Box,
  Table,
  TableRow,
  TableSortLabel,
  TableBody,
  TableContainer,
  TableHead,
  TablePagination
} from '@mui/material';
import {CollateralProps} from "config/constants/types";
import TableCell, {tableCellClasses} from '@mui/material/TableCell';
import SortIcon from './icon/SortIcon'
import Action from './Action'
import {useCollateralList} from "../../../state/hooks";
import {useAppSelector} from "../../../state";

const StyledTableCell = styled(TableCell)(() => ({
  [`&.${tableCellClasses.head}`]: {
    border: 'none',
    color: 'rgba(0, 0, 0, 0.5)'
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
    borderBottom: '1px solid #E2E8EE'
  },
}));
const StyledTableRow = styled(TableRow)(() => ({
  // hide last border
  '&:last-child td, &:last-child th': {
    fontSize: 14,
    borderBottom: '1px solid rgba(226, 232, 238, 1)'
  },
}));


function descendingComparator<T>(a: T, b: T, orderBy: keyof T) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

type Order = 'asc' | 'desc';

function getComparator<Key extends keyof any>(
  order: Order,
  orderBy: Key,
): (
  a: { [key in Key]: number | string },
  b: { [key in Key]: number | string },
) => number {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}


function stableSort<T>(array: T[], comparator: (a: T, b: T) => number) {
  const stabilizedThis = array.map((el, index) => [el, index] as [T, number]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) {
      return order;
    }
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

type action = 'action'

interface HeadCell {
  disablePadding: boolean;
  id: keyof CollateralProps | action;
  label: string;
  numeric: boolean;
}

const headCells = [
  {
    id: 'ticker',
    numeric: false,
    disablePadding: true,
    label: 'Ticker',
  },
  {
    id: 'liquidity',
    numeric: true,
    disablePadding: false,
    label: 'Liquidity',
  },
  {
    id: 'outstandingDebt',
    numeric: true,
    disablePadding: false,
    label: 'Outstanding Debt',
  },
  {
    id: 'collateralRatio',
    numeric: true,
    disablePadding: false,
    label: 'Collateral Ratio',
  },
  {
    id: 'guaranteeDays',
    numeric: true,
    disablePadding: false,
    label: 'Guarantee Days',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    label: 'Action',
  },
];

interface EnhancedTableProps {
  onRequestSort: (event: React.MouseEvent<unknown>, property) => void;
  order: Order;
  orderBy: string;
}

function EnhancedTableHead(props: EnhancedTableProps) {
  const {order, orderBy, onRequestSort} = props;
  const createSortHandler = (property) => (event: React.MouseEvent<unknown>) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            {headCell.id === 'action' || headCell.id === 'Avatar' ? <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              IconComponent={null}
            >
              {headCell.label}
              {orderBy === headCell.id ? (
                <Box component="span" style={{marginLeft: 4}}>
                  {/* {order === 'desc' ? 'sorted descending' : 'sorted ascending'} */}
                  <SortIcon order={order}/>
                </Box>
              ) : null}
            </TableSortLabel> : (
              <TableSortLabel
                active={orderBy === headCell.id}
                direction={orderBy === headCell.id ? order : 'asc'}
                onClick={createSortHandler(headCell.id)}
                IconComponent={null}
              >
                {headCell.label}
                {orderBy === headCell.id ? (
                  <Box component="span" style={{marginLeft: 4}}>
                    {/* {order === 'desc' ? 'sorted descending' : 'sorted ascending'} */}
                    <SortIcon order={order}/>
                  </Box>
                ) : null}
              </TableSortLabel>
            )
            }

          </StyledTableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

export default function EnhancedTable(props) {
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState('ticker');
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);
  const {collateralList} = props;
  // const collateralList = useAppSelector(state=> state.collateralManage.collateralList);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };
  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected: readonly string[] = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  // Avoid a layout jump when reaching the last page with empty rows.
  const emptyRows =
    page > 0 ? Math.max(0, (1 + page) * rowsPerPage - collateralList.length) : 0;

  // @ts-ignore
  return (
    <Box sx={{width: '100%'}} style={{height: '80%', overflowY: 'scroll'}}>
      <TableContainer>
        <Table
          sx={{minWidth: 750}}
          aria-labelledby="tableTitle"
        >
          <EnhancedTableHead
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
          />
          <TableBody>

            {collateralList && stableSort(collateralList, getComparator(order, orderBy))
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row, index) => {
                const labelId = `enhanced-table-checkbox-${index}`;
                // @ts-ignore
                return (
                  <StyledTableRow
                    hover
                    onClick={(event) => handleClick(event, row.ticker)}
                    role="checkbox"
                    tabIndex={-1}
                    key={row.ticker}
                  >
                    <StyledTableCell component="th" id={labelId} scope="row">{row.ticker}</StyledTableCell>
                    <StyledTableCell>{row.liquidity}</StyledTableCell>
                    <StyledTableCell>{row.outstandingDebt}</StyledTableCell>
                    <StyledTableCell>{row.collateralRatio}</StyledTableCell>
                    <StyledTableCell>{row.guaranteeDays}</StyledTableCell>
                    <StyledTableCell>
                      <Action row={row}/>
                    </StyledTableCell>
                  </StyledTableRow>
                );
              })}
            {emptyRows > 0 && (
              <StyledTableRow>
                <StyledTableCell colSpan={6}/>
              </StyledTableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={collateralList&& collateralList.length || 0}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />

    </Box>
  );
}
