import { InjectedConnector } from '@web3-react/injected-connector'
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { BscConnector } from '@binance-chain/bsc-connector'
import { ConnectorNames } from 'components/uikit'
import { WalletLinkConnector } from 'web3-react-walletlink-connector'
import {Web3Provider } from '@ethersproject/providers'
import {Contract} from "@ethersproject/contracts";
import { ALL_SUPPORTED_CHAIN_IDS, SupportedChainId } from '../config/constants/chains'
import { INFURA_NETWORK_URLS } from '../config/constants/infura'

const injected = new InjectedConnector({ supportedChainIds: ALL_SUPPORTED_CHAIN_IDS })

const walletconnect = new WalletConnectConnector({
  supportedChainIds: ALL_SUPPORTED_CHAIN_IDS,
  rpc: INFURA_NETWORK_URLS,
  qrcode: true,
})

const bscConnector = new BscConnector({ supportedChainIds: ALL_SUPPORTED_CHAIN_IDS })

export const walletlink = new WalletLinkConnector({
  url: INFURA_NETWORK_URLS[SupportedChainId.MAINNET],
  appName: 'Uniswap',
  supportedChainIds: ALL_SUPPORTED_CHAIN_IDS,
})
export const connectorsByName: { [connectorName in ConnectorNames]: any } = {
  [ConnectorNames.Injected]: injected,
  [ConnectorNames.WalletConnect]: walletconnect,
  [ConnectorNames.BSC]: bscConnector,
  [ConnectorNames.WalletLink]: walletlink,
}

// const provider = new providers.Web3Provider()

export const getContract = (signer=undefined,abi,address) => {
  if (signer && abi) {
    // eslint-disable-next-line new-cap
    return new Contract(address, abi,signer )
  }
  if (!signer) {
    return  new Contract(address, abi)
  }
  return false
}
export function getSigner(library, account) {
    return library.getSigner(account).connectUnchecked();
}

export const getLibrary = (provider): Web3Provider => {
  return new Web3Provider(provider);
}
