import React, {useState} from 'react';
import {styled} from "@mui/material/styles";
// eslint-disable-next-line import/named
import { PoolProps } from 'config/constants/types';
import Select from "../Select";



const SelectWrap = styled('div')`
  display: flex;
  justify-content: space-between;
`

const GroupWrap = styled('div')`
  display: flex;
  flex-direction: column;
  width: calc(100% / 2 - 25px);

  p {
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    box-sizing: border-box;

    span {
      font-size: 12px;
      line-height: 17px;
      color: rgba(0, 0, 0, 0.5);

    }
  }
`

export const IconWrap = styled('strong')`
  display: block;
  width: 20px;
  height: 20px;
  background: #C4C4C4;
  line-height: 20px;
  text-align: center;
  border-radius: 50%
`

export interface MethodsProps {
  options: PoolProps[] // selectIcon
  selectValue: number // current selected
  onMax?: (arg1: PoolProps)=>void // to max action
  selectedBalance: number // selected balance
  selectedSymbol: string // selected symbol
  SelectIcon: React.FC // leftTop icon
  RightIcon: React.FC // right icon
  leftInputValue?: number | null // left input value
  rightInputValue?: number // right input value
  rightBalance:number // right balance
  rightSymbol:string // right symbol
  rightOptions: PoolProps[] // options list
  rightSelectedValue: number // right currency value
  selectInput?: (arg1:React.SyntheticEvent)=> void,
  leftBalance?: any
}

export default function Index(props: MethodsProps) {
  const {options,leftBalance, onMax,selectInput,  SelectIcon,RightIcon, leftInputValue, rightInputValue,rightBalance} = props;
  const [currentSelected, setVal] = useState<PoolProps>(options[0]);
  const [rightList,setRightList] = useState<PoolProps[]>(options[0].rightOptionList)
  return (
    <>
      <SelectWrap>
        <GroupWrap>
          <p><SelectIcon /><span className="balance">{leftBalance} {currentSelected.symbol}</span></p>
          <Select key={1} selectValue={currentSelected.token} onChange={(val)=>{
            const selected = options.filter(it=>it.token === val)[0];
            setRightList(selected.rightOptionList)
            setVal(selected)
          }} selectInput={selectInput} actionValue={leftInputValue} options={options} groupStyle={{width: '100%'}} isGroup/>
          <p dir='rtl'>
            {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions,no-unused-expressions */}
            <span onClick={()=>{onMax && onMax(currentSelected);  }}
                  style={{cursor:'pointer',color:'#6ED6F3',fontSize:'0.75rem', userSelect:"none"}}
            >Max</span>
          </p>
        </GroupWrap>
        <GroupWrap>
          <p>
            <RightIcon />
            <span className="balance">{rightBalance} {rightList[0].symbol}</span></p>
          <Select key={2} selectValue={rightList[0].token} options={rightList}   actionValue={rightInputValue} groupStyle={{width: '100%'}} isGroup allDisabled />
        </GroupWrap>
      </SelectWrap>
    </>
  );
}
