import React, {useCallback, useEffect, useState} from 'react';
import {InputBase} from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';
import {styled} from '@mui/material/styles';
import Card from 'components/Card'
import SwitchButton from "components/Switch";
import { BG_COMMON_COLOR } from 'components/uikit/widgets/Menu/config';
import {useManagerList} from "hooks/useManager";
import {useWeb3React} from "web3-react-core";
import {OpenStatus} from "config/constants/types";
import Table from './components/Table'
import Title from "./components/Title";

export const Search = styled('div')(() => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  position: 'relative',
  boxSizing: 'border-box',
  borderRadius: 11,
  height: 44,
  marginBottom: 34
}));

export const SearchIconWrapper = styled('div')(({theme}) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

export const StyledInputBase = styled(InputBase)(({theme}) => ({
  color: 'inherit',
  width: '100%',
  height: '44px',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    background: `${BG_COMMON_COLOR}`,
    boxSizing: "border-box",
    borderRadius: "11px",
    border: '1px solid rgba(207, 216, 226, 0.5)',
    transition: theme.transitions.create('width'),
    width: '100%',
    height: '100%',
    caretColor: '#6ED6F3',
    '&::-webkit-search-cancel-button': {
      height: "1em",
      width: "1em",
      borderRadius: '50em',
      background: 'url(https://pro.fontawesome.com/releases/v5.10.0/svgs/solid/times-circle.svg) no-repeat 50% 50%',
      backgroundSize: 'contain',
      opacity: "0.3",

    }
  }
}));

function Collateral() {
  const managerList = useManagerList();
  const {chainId, account} = useWeb3React();
  const [searchVal, setSearchVal] = useState();

  const [holderText, setPlaceholder] = useState<string>('Ticker')
  const [collateralList,setCollateralList] = useState()

  const onSearch = useCallback((event) => {
    setSearchVal(event.target.value);
  }, [setSearchVal]);
  const initCollateralList = useCallback(async ()=>{
    if (managerList && account) {
      const collateral = [];
      // eslint-disable-next-line no-restricted-syntax
      for (const manager of managerList) {
        const data = {
          ticker : manager.symbol,
          liquidity: 0,
          outstandingDebt: 0,
          collateralRatio: 0,
          guaranteeDays : 0,
          status: OpenStatus.notOpen,
          avatar: '',
          managerAddress: manager.address[chainId],
        }
        try {
          // eslint-disable-next-line no-await-in-loop
          const position = await manager.contract.positions(account);
          if (Number(position.toString())) {
            data.avatar = position;
            data.status = OpenStatus.opened;
          }
        } catch (e) {
          data.status = OpenStatus.notOpen;
          data.avatar = '0x0000000000000000000000000000000000000000';
        }

        collateral.push(data)
      }
      // @ts-ignore
      setCollateralList(collateral)
    }
  }, [account, chainId, managerList])
  /**
   * init managerList
   * */
  useEffect(()=>{
    initCollateralList();
  }, [initCollateralList])


  return (
    <div style={{
      width: '100%', height: '100%',
      paddingRight: 100
    }}>
      <Title>Collateral</Title>
      <Card style={{height: 'calc(100% - 200px)'}}>
        <Search>
          <SearchIconWrapper>
            <SearchIcon/>
          </SearchIconWrapper>
          <StyledInputBase
            type="search"
            placeholder={holderText}
            inputProps={{'aria-label': 'search'}} value={searchVal} onInput={onSearch}
            onFocus={() => {
              setPlaceholder('')
            }}
            onBlur={()=>{
              setPlaceholder('Ticker')
            }}
          />
          <SwitchButton/> <span>Opened</span>
        </Search>
        <Table collateralList={collateralList} />
      </Card>
    </div>
  );
}

export default Collateral;
