import {BigNumber} from "@ethersproject/bignumber";
import tokens from 'config/constants/tokens'

export function formatWBTC(val: BigNumber ){
  if (val && !val._isBigNumber) return NaN;

  if (val) {
    // @ts-ignore
    return val / (BigNumber.from(10).pow(BigNumber.from(tokens.WBTC.decimals)))
  }
  return 0
}

export default function formatTheValue(val: number | BigNumber, decimals) {
  if (val instanceof BigNumber) {
    return val.toNumber() / 10**decimals
  }
  return val / 10**decimals
}
