import * as React from 'react';
import Slider  from '@mui/material/Slider';
import { styled } from '@mui/material/styles';


const PrettoSlider = styled(Slider)({
  color: '#52af77',
  height: 320,
  width: 6,
  '& .MuiSlider-rail': {
    background:'linear-gradient(180deg, #1DCADA, #FFCC04 40%, #FF6308)',
    opacity: '1',
  },
  '& .MuiSlider-track': {
    border: 'none',
    background: "transparent"
    // background:'linear-gradient(90deg, #FF4D00 0%, #FF730D 31.52%, #FFBD12 44.08%, #FFD900 97.25%)',
  },
  '& .MuiSlider-thumb': {
    height: 12,
    width: 12,
    background:'#fff',
    transform: 'translate(-30px,50%)',
    // background:'linear-gradient(90deg, #FF4D00 0%, #FF730D 31.52%, #FFBD12 44.08%, #FFD900 97.25%)',
    border: '5px solid #000',
    '&:focus, &:hover, &.Mui-active, &.Mui-focusVisible': {
      boxShadow: 'inherit',
    },
    '&:before': {
      position: 'absolute',
      left: '4px   ' ,
      zIndex: -1,
      width: 21,
      height: 2,
      background: '#000',
    },
  },
  '& .MuiSlider-valueLabel': {
    lineHeight: 1.2,
    fontSize: 12,
    background: 'unset',
    padding: 0,
    width: 32,
    height: 32,
    borderRadius: '50% 50% 50% 0',
    backgroundColor: '#52af77',
    transformOrigin: 'left',
    transform: 'translate(50%, -100%) rotate(-90deg) scale(0)',
    '&:before': { display: 'none' },
    '&.MuiSlider-valueLabelOpen': {
      display: 'none',
      transform: 'translate(50%, -100%) rotate(-90deg) scale(1)',
    },
    '& > *': {
      transform: 'rotate(45deg)',
    },
  },
});

export default function CustomizedSlider(props) {
  return (
      <PrettoSlider
        valueLabelDisplay="auto"
        aria-label="pretto slider"
        orientation="vertical"
        defaultValue={90}
        disabled
        {...props}
      />
  );
}
